import { SpriteFrame } from 'cc';
import { Label } from 'cc';
import { Sprite } from 'cc';
import { _decorator, Component, Node } from 'cc';
import PortraitMgr from 'db://assets/core_tgx/base/PortraitMgr';
import { Num } from 'db://assets/core_tgx/framework/util/Num';
import { instantiate } from 'cc';
import { Prefab } from 'cc';
import { Button } from 'cc';
import { EventHandler } from 'cc';
import { ConfigMgr } from '../../scripts/ConfigMgr';
const { ccclass, property } = _decorator;

@ccclass('shopMgr')
export class shopMgr extends Component {
    @property(Node)
    popUp:Node;
    @property(Sprite)
    coins:Sprite;
    @property(SpriteFrame)
    imgdata: SpriteFrame[] = [];
    @property(Label)
    purchaseNum:Label;
    @property(Node)
    topcharge:Node;
    @property(Prefab)
    chongzhipb:Prefab;

    start() {
        let res = ConfigMgr.inst.getShopConfig();
        let chongzhiif = res.chongzhi;
        for(let i = 0;i<chongzhiif.length;i++){
            let temp = instantiate(this.chongzhipb);
            if(temp.getChildByName("coins")){
                temp.getChildByName("coins").getComponent(Sprite).spriteFrame = this.imgdata[chongzhiif[i].img]
            }
            if(temp.getChildByName("num")){
                temp.getChildByName("num").getChildByName("Label").getComponent(Label).string = chongzhiif[i].moneyNum;
            }
            temp.on(Node.EventType.TOUCH_END,()=>{
                this.onclickCard(chongzhiif[i].id);
            });
                    // 获取按钮组件
        // const btn = temp.getComponent(Button);

        // // 创建点击事件处理器
        // const clickEvent = new EventHandler();
        // clickEvent.target = this.node; // 当前脚本绑定的节点
        // clickEvent.component = "shopMgr"; // 👈 换成你这个脚本类的类名（不是文件名）
        // clickEvent.handler = "onclickCard";
        // clickEvent.customEventData = String(i + 1); // 传递 "1" ~ "6"

        // btn.clickEvents.push(clickEvent);
            this.topcharge.addChild(temp);
        }
    }

    onclickCard(args:any){
        switch(args){
            case"1":
            this.coins.spriteFrame = this.imgdata[0];
            this. openpopUp();
            break;
            case"2":
            this.coins.spriteFrame = this.imgdata[1];
            this. openpopUp();
            break;
            case"3":
            this.coins.spriteFrame = this.imgdata[2];
            this. openpopUp();
            break;
            case"4":
            this.coins.spriteFrame = this.imgdata[3];
            this. openpopUp();
            break;
            case"5":
            this.coins.spriteFrame = this.imgdata[4];
            this. openpopUp();
            break;
            case"6":
            this.coins.spriteFrame = this.imgdata[5];
            this. openpopUp();
            break;

        }
    }
    
    openpopUp(){
        this.popUp.active = true;
    }

    closepopUp(){
        this.popUp.active = false;
    }

    add(){
       let temp = Num.parse(this.purchaseNum.string) + 1;
       this.purchaseNum.string = temp.toString();
    }

    sub(){
       if(this.purchaseNum.string == "1"){

       }else{
        let temp = Num.parse(this.purchaseNum.string) - 1;
        this.purchaseNum.string = temp.toString();
       }
    }

    update(deltaTime: number) {
        
    }
}


