import { PageView } from 'cc';
import { Label } from 'cc';
import { Prefab } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { instantiate } from 'cc';
import { SlotBetNum } from './SlotBetNum';
import { tween } from 'cc';
import { Num } from 'db://assets/core_tgx/framework/util/Num';
import { view } from 'cc';
import { Vec3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('SlotBetPanel')
export class SlotBetPanel extends Component {
    @property({ type: Node, tooltip: "" })
    public panel: Node = null!;
    @property({ type: Prefab, tooltip: "" })
    public chooseBet_pb: Prefab = null!;
    @property({ type: Prefab, tooltip: "" })
    public chooseTotalBet_pb: Prefab = null!;
    @property({ type: PageView, tooltip: "" })
    public chooseBet_list1: PageView = null!;
    @property({ type: PageView, tooltip: "" })
    public chooseBet_list2: PageView = null!;
    @property({ type: PageView, tooltip: "" })
    public chooseBet_list3: PageView = null!;
    @property({ type: Label, tooltip: "" })
    public lbLine: Label = null!;
    @property({ type: Label, tooltip: "用户金币" })
    public lblUserCoin: Label = null!;
    @property({ type: Label, tooltip: "本局总注" })
    public lblCurBet: Label = null!;
    @property({ type: Label, tooltip: "赢钱金额" })
    public winCoin_lab: Label = null!;

    private _money: number = 0;
    public get money(): number {
        return this._money;
    }
    public set money(m: number) {
        this._money = m;
    }

    //汇率
    private _exchangeRate: number = 0;
    public get exchangeRate(): number {
        this._exchangeRate = 1;
        return this._exchangeRate;
    }

    /**累计下注 */
    private _betSum: number = 0;
    public get betSum() {
        return this._betSum;
    }
    public set betSum(v: number) {
        this._betSum = v;
    }

    private _callback: Function = null;

    protected update(dt: number): void {
        this.lblUserCoin.string = Num.exchangeToThousands(this.exchangeRate, this.money);
        this.lblCurBet.string = Num.exchangeToThousands(this.exchangeRate, this.betSum);
        // this.winCoin_lab.string = mouse.slotCtrl.winCoin_lab.string;
    }

    numList: number[] = [];
    betList: number[] = [];
    line: number = 0;
    val1: number = 0;
    val2: number = 0;
    val3: number = 0;
    private tempi: number = 0; //投注大小下标位
    private tempj: number = 0; //投注倍数下标位
    private tempindex: number = 0; //投注总额下标位
    private sumList: number[] = []; //投注总额组

    protected onLoad(): void {
        // const v = view.getVisibleSize();
        // const s = v.height / 1334;
        // this.node.scale = new Vec3(s, s, 1);
    }

    initBetContent() {
        this.val1 = this.numList[0];
        this.val2 = this.betList[0];
        this.val3 = this.numList[0] * this.betList[0] * this.line;
        this.chooseBet_list1.removeAllPages();
        for (let i = 0; i < this.numList.length; i++) {
            let newNode = instantiate(this.chooseBet_pb);
            newNode.children[0].active = (i == 0);
            newNode.children[1].active = (i == this.numList.length - 1);
            newNode.getComponent(Label).string = Num.exchangeToThousands(this.exchangeRate, this.numList[i]);
            newNode.getComponent(SlotBetNum).valNum = this.numList[i];
            this.chooseBet_list1.addPage(newNode);
        }
        this.chooseBet_list2.removeAllPages();
        for (let i = 0; i < this.betList.length; i++) {
            let newNode = instantiate(this.chooseBet_pb);
            newNode.children[0].active = false;
            newNode.children[1].active = false;
            newNode.getComponent(Label).string = this.betList[i] + "";
            newNode.getComponent(SlotBetNum).valNum = this.betList[i];
            this.chooseBet_list2.addPage(newNode);
        }
        //动态计算第三列的值
        let list = [];
        for (let i = 0; i < this.numList.length; i++) {
            for (let j = 0; j < this.betList.length; j++) {
                list.push(this.numList[i] * 1000 * this.betList[j] * this.line / 1000);
            }
        }
        let sumList = Array.from(new Set(list));
        sumList.sort((a, b) => {
            return a - b;
        });
        this.sumList = sumList;
        this.chooseBet_list3.removeAllPages();
        for (let i = 0; i < sumList.length; i++) {
            let newNode = instantiate(this.chooseTotalBet_pb);
            newNode.children[0].active = (i == 0);
            newNode.children[1].active = (i == sumList.length - 1);
            newNode.getComponent(Label).string = Num.exchangeToThousands(this.exchangeRate, sumList[i]);
            newNode.getComponent(SlotBetNum).valNum = sumList[i];
            this.chooseBet_list3.addPage(newNode);
        }
        this.lbLine.string = this.line + "";
        this.chooseBet_list1.node.on('page-turning', this.cb_pt1, this);
        this.chooseBet_list2.node.on('page-turning', this.cb_pt2, this);
        this.chooseBet_list3.node.on('page-turning', this.cb_pt3, this);
    }

    private cb_pt1(pageView: PageView) {
        this.val1 = this.numList[pageView.getCurrentPageIndex()];
        //更新指示器位置
        for (let i = 0; i < this.chooseBet_list3.getPages().length; i++) {
            if (this.chooseBet_list3.getPages()[i].getComponent(SlotBetNum).valNum == this.val1 * 1000 * this.val2 * this.line / 1000) {
                this.chooseBet_list3.setCurrentPageIndex(i);
                this.val3 = this.chooseBet_list3.getPages()[i].getComponent(SlotBetNum).valNum;
                this.tempindex = i;
                break;
            }
        }
        this.tempi = pageView.getCurrentPageIndex();
    }

    private cb_pt2(pageView: PageView) {
        this.val2 = this.betList[pageView.getCurrentPageIndex()];
        //更新指示器位置
        for (let i = 0; i < this.chooseBet_list3.getPages().length; i++) {
            if (this.chooseBet_list3.getPages()[i].getComponent(SlotBetNum).valNum == this.val1 * 1000 * this.val2 * this.line / 1000) {
                this.chooseBet_list3.setCurrentPageIndex(i);
                this.val3 = this.chooseBet_list3.getPages()[i].getComponent(SlotBetNum).valNum;
                this.tempindex = i;
                break;
            }
        }
        this.tempj = pageView.getCurrentPageIndex();
    }

    cb_pt3(pageView: PageView) {
        this.val3 = this.sumList[pageView.getCurrentPageIndex()];
        this.tempindex = pageView.getCurrentPageIndex();
        //更新指示器位置
        for (let i = 0; i < this.chooseBet_list1.getPages().length; i++) {
            for (let j = 0; j < this.chooseBet_list2.getPages().length; j++) {
                if (this.val3 == this.chooseBet_list1.getPages()[i].getComponent(SlotBetNum).valNum * 1000 * this.chooseBet_list2.getPages()[j].getComponent(SlotBetNum).valNum * this.line / 1000) {
                    this.chooseBet_list1.setCurrentPageIndex(i);
                    this.chooseBet_list2.setCurrentPageIndex(j);
                    this.val1 = this.chooseBet_list1.getPages()[i].getComponent(SlotBetNum).valNum;
                    this.val2 = this.chooseBet_list2.getPages()[j].getComponent(SlotBetNum).valNum;
                    this.tempi = i;
                    this.tempj = j;
                    break;
                }
            }
        }
    }

    //选择最大下注
    private choose_Max() {
        this.val3 = this.sumList[this.sumList.length - 1];
        this.tempindex = this.sumList.length - 1;
        //更新指示器位置
        for (let i = 0; i < this.chooseBet_list1.getPages().length; i++) {
            for (let j = 0; j < this.chooseBet_list2.getPages().length; j++) {
                if (this.val3 == this.chooseBet_list1.getPages()[i].getComponent(SlotBetNum).valNum * 1000 * this.chooseBet_list2.getPages()[j].getComponent(SlotBetNum).valNum * this.line / 1000) {
                    this.chooseBet_list1.setCurrentPageIndex(i);
                    this.chooseBet_list2.setCurrentPageIndex(j);
                    this.val1 = this.chooseBet_list1.getPages()[i].getComponent(SlotBetNum).valNum;
                    this.val2 = this.chooseBet_list2.getPages()[i].getComponent(SlotBetNum).valNum;
                    this.tempi = i;
                    this.tempj = j;
                    break;
                }
            }
        }
    }

    private onConfirm() {
        this.betSum = this.sumList[this.tempindex];
        this.lblCurBet.string = Num.exchangeToThousands(this.exchangeRate, this.betSum);
        // mouse.slotCtrl.lblCurBet.string = this.lblCurBet.string;
        this._callback && this._callback(this.tempi, this.tempj, this.tempindex, this.sumList[this.tempindex], this.lblCurBet.string);
        this.onClose();
    }

    bindBet(index: number) {
        this.chooseBet_list3.setCurrentPageIndex(index);
        this.val3 = this.betSum;
        this.cb_pt3(this.chooseBet_list3);
    }

    private showPos: number;
    private hidePos: number;
    /**
     * 
     * @param betNum 单注值
     * @param bet 
     * @param lines 线数
     * @param args 0:单注值  1：下注倍数  2：线数  3：持有金币 4:下注额  5：回调
     */
    showPanel(...args) {
        // mouse.node.audio.playclickopen();

        this.numList = args[0];
        this.betList = args[1];
        this.line = args[2];
        this.money = args[3];
        this._betSum = args[4]
        this._callback = args[args.length - 1] instanceof Function ? args[args.length - 1] : null;
        this.winCoin_lab.string = "0";

        this.node.active = true;
        this.showPos = (this.panel.h - this.node.parent.h) / 2;
        // this.hidePos = -this.panel.h / 2;
        this.panel.y = this.showPos - this.panel.h;
        this.panel.children[0].opacity = 10;
        this.initBetContent();
        tween(this.panel)
            .to(0.2, { y: this.showPos })
            .start()
        tween(this.node)
            .delay(0.2)
            .call(() => {
                tween(this.panel.children[0])
                    .to(0.2, { opacity: 255 })
                    .start()
            })
            .start()
    }

    hidePanel() {
        tween(this.panel)
            .to(0.2, { y: this.showPos - this.panel.h })
            .call(() => {
                this.node.active = false;
            })
            .start()
    }

    onClose() {
        // mouse.node.audio.playclickclose();
        this.hidePanel();
    }
}


