import { SpriteFrame } from 'cc';
import { Label } from 'cc';
import { Prefab } from 'cc';
import { Sprite } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { Num } from 'db://assets/core_tgx/framework/util/Num';
import { instantiate } from 'cc';
import { ConfigMgr } from '../../scripts/ConfigMgr';
const { ccclass, property } = _decorator;

@ccclass('shopGiftMgr')
export class shopGiftMgr extends Component {
 @property(Node)
    popUp:Node;
    @property(Sprite)
    coins:Sprite;
    @property(SpriteFrame)
    imgdata: SpriteFrame[] = [];
    @property(Label)
    purchaseNum:Label;
    @property(Node)
    giftnode:Node;
    @property(Prefab)
    giftpb:Prefab;
    private spealist:boolean = true;
    start() {
                let res = ConfigMgr.inst.getShopConfig();
                let giftlist = res.gift;
        for(let i = 0;i<giftlist.length;i++){
                    let temp = instantiate(this.giftpb);
                    if(temp.getChildByName("gift")){
                        temp.getChildByName("gift").getComponent(Sprite).spriteFrame = this.imgdata[giftlist[i].img]
                    }
                    if(temp.getChildByName("buy_btn")){
                        temp.getChildByName("buy_btn").getChildByName("num").getComponent(Label).string = giftlist[i].moneyNum;
                    }
                    // if(this.spealist){
                    //     temp.children[1].active = true;
                    //     this.spealist = false;
                    // }
                    temp.on(Node.EventType.TOUCH_END,()=>{
                        this.onclickCard(giftlist[i].id);
                    });
                            // 获取按钮组件
                // const btn = temp.getComponent(Button);
        
                // // 创建点击事件处理器
                // const clickEvent = new EventHandler();
                // clickEvent.target = this.node; // 当前脚本绑定的节点
                // clickEvent.component = "shopMgr"; // 👈 换成你这个脚本类的类名（不是文件名）
                // clickEvent.handler = "onclickCard";
                // clickEvent.customEventData = String(i + 1); // 传递 "1" ~ "6"
        
                // btn.clickEvents.push(clickEvent);
                    this.giftnode.addChild(temp);
                }
    }

    onclickCard(args:any){
        switch(args){
            case"1":
            this.coins.spriteFrame = this.imgdata[0];
            this. openpopUp();
            break;
            case"2":
            this.coins.spriteFrame = this.imgdata[1];
            this. openpopUp();
            break;
            case"3":
            this.coins.spriteFrame = this.imgdata[2];
            this. openpopUp();
            break;
            case"4":
            this.coins.spriteFrame = this.imgdata[3];
            this. openpopUp();
            break;
            case"5":
            this.coins.spriteFrame = this.imgdata[4];
            this. openpopUp();
            break;
            case"6":
            this.coins.spriteFrame = this.imgdata[5];
            this. openpopUp();
            break;
            case"7":
            this.coins.spriteFrame = this.imgdata[6];
            this. openpopUp();
            break;

        }
    }

    openpopUp(){
        this.popUp.active = true;
    }

    closepopUp(){
        this.popUp.active = false;
    }

    add(){
       let temp = Num.parse(this.purchaseNum.string) + 1;
       this.purchaseNum.string = temp.toString();
    }

    sub(){
       if(this.purchaseNum.string == "1"){

       }else{
        let temp = Num.parse(this.purchaseNum.string) - 1;
        this.purchaseNum.string = temp.toString();
       }
    }

    update(deltaTime: number) {
        
    }
}


