export const enum e_role_status {
    Normal,
    Match,
    Play,
    HOSTING,
}

/** 房间状态 */
export const enum e_room_status {
    Normal,
    /** 只有一个人的时候是等待状态 */
    Waiting,
    /** 2 个人以上进入准备状态 */
    Ready,
    /** 游戏阶段 */
    Play,
    /** 结算 */
    Settle,
    /** 结算完成 */
    SettleFinish
}

/** 玩家信息 */
export interface i_player {
    /** 玩家id */
    uid: string;
    /** 玩家身上的金币 -1表示未知 */
    gold: number;
    /** 座位 */
    seat: number;
    /** 座位类型 0普通座位 1庄家 2小盲 3大盲 4弃牌*/
    seat_type: number;
    /** 昵称 */
    nickname: string;
    /** 头像 */
    head: string;
    /** 头像序号 */
    pic: number;
    /** 本局下注 */
    bet: number;
    /**本轮下注 */
    round_bet: number;
    /**当前轮最后一次操作 */
    last_action?: number,

    /** 玩家状态 1匹配中 2游戏中 3托管中*/
    status: e_role_status;
    /** 玩家的手牌,如果当前玩家是全押状态,下发手牌信息 */
    hand_ids: number[];
    /** 牌型 */
    card_type: number;
    /** 全押的轮次 */
    allin_round: number;
}

/** 重连游戏下发结构 */
export interface i_resume {

    /** 房间id */
    room_id: string,
    /** 房间状态 */
    room_status: number,
    /** 轮次状态 */
    round_status: number,
    /** 公共牌 */
    comm_ids: number[],
    /** 玩家的手牌 */
    hand_ids: number[],
    /** 牌型 */
    card_type: number,
    /** 底池 */
    main_pot: number,
    /** 玩家信息 */
    players: i_player[],

    /** 当前出牌玩家座位 */
    op_seat: number,
    /** 操作时间 */
    op_time: number,

    /** 本轮累计下注 */
    round_bets: number[],
    /** 房间底分 */
    bet: number
    /**是否可以下注 */
    can_bet: number;
    /**最小下注金额(也是最大下注金额) */
    min_bet: number;
    /**是否可过牌 */
    can_check: number;
    /**是否可以跟牌 */
    can_call: number;
    /**跟注金额 */
    call: number;

    /**是否可以加注 */
    can_raise: number;
    /** 最小跟注金额 */
    min_raise: number;
}


/** 下发给客户端的数据结构 */
/** 开始游戏下发结构 */
export interface i_sg {
    /** 房间id */
    room_id: string;
    /** 房间状态 */
    room_status: number;
    /** 发牌时间 */
    licensing_time: number;
    /** 操作时间 */
    action_time: number;
    /** allin 时手牌展示时间 */
    allin_time: number;
    /** 庄家位 */
    button_seat: number;
    /** 小盲位 */
    sb_seat: number;
    /**大盲位 */
    bb_seat: number;
    /** 小盲注 */
    sb_bet: number,
    /** 大盲注 */
    bb_bet: number;
}