import { PlaySlotRound } from "./Match3"

export enum Dragon {
    EARTH,//土龙， 10个
    WATER,//水龙， 30个
    FIRE,//火龙, 50个
    DRAGON,//巨龙， 70个
}

//寻龙探宝
export interface DragonHatchRound extends PlaySlotRound {
    dragons: Dragon[];//本轮结束后剩余的龙
    addDragon?: Dragon;//本轮新增的龙
    usedDragon?: Dragon; //本轮使用的龙
}