
import { I_card } from "./card";

/** 房间状态 */
export enum e_room_status {
    Normal,
    DengDai,
    Ready,
    QiangZhuang,
    ShowRandomZhuang,
    XiaZhu,
    TanPai,
    JieSuan,
}


export interface i_player {
    uid: string;
    gold: string;
    seat: number;
    nickname: string;
    head: string;
    pic: number;
}

/** 下发给客户端的数据结构 */
export interface i_sg {
    /** 操作玩家的位置(座位) */
    op_seat: number;
    /** 可以下注的倍数 */
    used_zhuang: number[];
    /** 房间id */
    room_id: string;
    /** 时间戳 */
    timestamp: number;
    /** 房间状态 */
    room_status: e_room_status;
    /** 下注列表 */
    bet_list: number[];
    /** 可下注的庄的列表 */
    z_list: number[];
    /** 庄的位置 */
    z_seat: number;
    /** 玩家信息 */
    players: i_player[];
    /** 操作时间 */
    op_time: number;
    /** 庄家的倍数 */
    z_scale: number;
    /** 玩家的牌型 */
    cards: any;
    /** 房间底分 */
    room_scale?: number;
}

/** 抢庄下发数据结构 */
export interface i_qz {
    seat: number;
    scale: number;
    //是否放弃抢庄
    isGiveUp: boolean;
    op_seat: number;
    op_time: number;
    qiangzhuangArr: { uid: string, seat: number, scale: number, isGiveUp: boolean }[];
    room_status: e_room_status;
    used_zhuang: number[];
    timestamp: number;
    z_seat: number;
    z_scale: number;
}

export interface i_xz {
    bet_list: number[];
    op_time: number;
    room_status: e_room_status;
    timestamp: number;
    z_seat: number;
    z_scale: number;
}

/** interface gameover */
export interface i_gov {
    uid: string;
    gold: number;
    seat: number;
    is_win: number;
    win_gold: number;
    user_scale: number;
    type: number;
    scale: number;
    card: I_card[];
}

/** 结算的时候输赢枚举 */
export const enum e_win_fail {
    Normal,
    fail,
    win,
}