/**
 * @en player data of tank game
 * @zh 坦克游戏玩家数据
*/
export interface ITankPlayer {
    /**
     * @en user id, corresponding to the uid in database
     * @zh 用户 id，对应数据库中的 uid 
    */
    uid: string,
    /**
     * @zh the color of player‘s tank, used to distinguish different camp
     * @zh 玩家坦克颜色，用于区别不同阵营
    */
    color: number,
    /**
     * @en player id, use playerId instead of uid, because playerId in number type is more bandwidth-saving
     * @zh 玩家唯一 id。游戏逻辑使用 playerId 而不是 uid，因为 playerId 更省带宽
    */
    playerId:number,
    /**
     * @en properties of game play
     * @zh 游戏逻辑相关属性
    */
    x:number,
    y:number,
    z:number,
    rotation:number,
    hp:number,
    maxHp:number,
    score:number,
    /**
     * @en <= 0: alive, > 0: dead
     * @zh 复活时间 <= 0 活着 > 0 死亡
    */
    reviveTime:number,
}

/**
 * @en game data of tank
 * @zh 坦克游戏数据
*/
export interface ITankGameData {
    /**
     * @en the player list of the game
     * @zh 参与游戏的玩家列表
    */
    players: ITankPlayer[],
}