import { director, instantiate, RichText, Node, Widget } from "cc";
import { ModuleDef } from "../../scripts/ModuleDef";
import { GameUILayers } from "../../scripts/GameUILayers";
import { Layout_UIHelp } from "./Layout_UIHelp";
import { Sprite } from "cc";
import { ResourceMgr } from "../../core_tgx/base/ResourceMgr";
import { SubGameMgr } from "../scripts/SubGameMgr";
import { getSubGameConf, SubGameDef } from "../scripts/SubGameDef";
import { SpriteFrame } from "cc";
import { EventTouch } from "cc";
import { Vec2 } from "cc";
import { math } from "cc";
import { NodeEventType } from "cc";
import { LanguageData } from 'db://assets/module_basic/scripts/language/LanguageData';
import { game } from "cc";
import { ConfigMgr } from "../scripts/ConfigMgr";

//帮助参数
export class UIhelpParam
{
    /**可以不填，不填的话 默认读取当前游戏的gamekey */
    gameKey: string = "";
    type: string;
    constructor(type:string,gamekey: string = "") {    
        this.gameKey = gamekey;
        this.type = type;
    }
}

@tgx_class(ModuleDef.BASIC)
export class UIHelp extends tgx.UIController {
    private _helpImgs: string[] = [];
    private _helpIndex:number = 0;
    // 触摸起始位置
    private startPos: Vec2 = new Vec2();
    private _pageList:Node[];

    constructor() {
        super('ui_help/HelpPrefabPortrait', GameUILayers.POPUP2, Layout_UIHelp);
    }
    protected onCreated(params:UIhelpParam): void {
        var gameKey:string = params.gameKey;
        if(gameKey == "")
        {
            gameKey = SubGameMgr.curGameType;
        }

        let layout = this._layout as Layout_UIHelp;
        this.onButtonEvent(layout.btn_back, () => {
            this.close();
        });

        var ruleConfig:any = ConfigMgr.inst.getRuleConfig();
        if(!ruleConfig) return;
        var gameConfig:any = ruleConfig[gameKey];
        if(!gameConfig) return;
        var helpConfig = gameConfig[params.type];
        if(!helpConfig) return;
        

        
        let languageHelp:any = helpConfig[LanguageData.current];
        if(!languageHelp)
        {
            return;
        }
        let imgPath:string = helpConfig[LanguageData.current].path;
        let count:number = helpConfig[LanguageData.current].count;
        this._helpImgs = [];
        for(var i:number = 0;i < count;i++)
        {
            this._helpImgs.push(imgPath+Number(i+1));
        }
        if(!this._helpImgs) return;
        this._pageList = [];
        for(var i:number = 0;i < this._helpImgs.length;i++)
        {
            
            var prefabInstance = instantiate(layout.node_page);
            prefabInstance.active = true;
            layout.list_page.addChild(prefabInstance);
            this._pageList.push(prefabInstance);
        }
        layout.txt_title.string = LanguageData.getLangByID(params.type);

        

        this._helpIndex = 0;
        this.showImage();
        // 监听触摸开始事件
        this._layout.portraitNode.on(NodeEventType.TOUCH_START, this.onTouchStart, this);
        // 监听触摸结束事件
        this._layout.portraitNode.on(NodeEventType.TOUCH_END, this.onTouchEnd, this);
    }
    protected onDispose() {
        // 监听触摸开始事件
        this._layout.portraitNode.off(NodeEventType.TOUCH_START, this.onTouchStart, this);
        // 监听触摸结束事件
        this._layout.portraitNode.off(NodeEventType.TOUCH_END, this.onTouchEnd, this);
    }
    private onTouchStart(event: EventTouch) {
         // 获取触摸开始时的世界坐标
         const worldStartPos = event.getLocation();
         // 将世界坐标转换到当前节点的本地坐标
         this.startPos = this.worldToLocal(worldStartPos);
    }

    private onTouchEnd(event: EventTouch) {
        // 获取触摸结束时的世界坐标
        const worldEndPos = event.getLocation();
        // 将世界坐标转换到当前节点的本地坐标
        const endPos = this.worldToLocal(worldEndPos);

        const deltaX = endPos.x - this.startPos.x;

        // 判断滑动方向
        if (deltaX > 30) {
            // 向右滑动，显示上一张图片
            this.prevImage();
        } else if (deltaX < -30) {
            // 向左滑动，显示下一张图片
            this.nextImage();
        }
    }
    private worldToLocal(worldPos: Vec2): Vec2 {
        const node = this._layout.node;
        // 获取节点的世界变换矩阵的逆矩阵
        const inverseWorldMatrix = node.worldMatrix.clone().invert();
        const a = inverseWorldMatrix.m00;
        const b = inverseWorldMatrix.m01;
        const c = inverseWorldMatrix.m04;
        const d = inverseWorldMatrix.m05;
        const tx = inverseWorldMatrix.m12;
        const ty = inverseWorldMatrix.m13;

        // 手动计算二维向量的变换
        const localX = a * worldPos.x + c * worldPos.y + tx;
        const localY = b * worldPos.x + d * worldPos.y + ty;
        return new Vec2(localX, localY);
    }
    private prevImage():void
    {
        this._helpIndex--;
        if(this._helpIndex>=0)
        {
            this.showImage();
        }
        else
        {
            this._helpIndex = 0;
        }
        
    }
    private nextImage():void
    {
        this._helpIndex++;
        if(this._helpIndex<this._helpImgs.length)
        {
            this.showImage();
        }
        else
        {
            this._helpIndex = this._helpImgs.length-1;
        }
    }

    private showImage():void
    {
        let conf = getSubGameConf(SubGameMgr.gameMgr.gameType);
        if (!conf) {
            return;
        }
        if(this._helpIndex>=this._helpImgs.length)
        {
            return;
        }
        ResourceMgr.inst.loadResByModuleBundle(conf.bundle,this._helpImgs[this._helpIndex]+"/spriteFrame", SpriteFrame, (error, resObj: SpriteFrame) => {
            let layout = this._layout as Layout_UIHelp;
            layout.img_help.getComponent(Sprite).spriteFrame = resObj;
        }); 
        for(var i:number = 0;i < this._pageList.length;i++)
        {
            
            this._pageList[i].scale = new math.Vec3(1,1,1);
        }
        var pageNode:Node = this._pageList[this._helpIndex];
        pageNode.scale = new math.Vec3(1.6,1.6,1);
    }
}