import { Label } from 'cc';
import { view } from 'cc';
import { UITransform } from 'cc';
import { Vec3 } from 'cc';
import { tween } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { Num } from 'db://assets/core_tgx/framework/util/Num';
const { ccclass, property } = _decorator;

@ccclass('SlotLastMoneyPanel')
export class SlotLastMoneyPanel extends Component {
    @property({ type: Node, tooltip: "" })
    public panel: Node = null!;
    @property({ type: Label, tooltip: "" })
    public lblUserCoin: Label = null!;

    private _money = 0;
    //汇率
    private _exchangeRate: number = 0;
    public get exchangeRate(): number {
        this._exchangeRate = 1;
        return this._exchangeRate;
    }

    protected update(dt: number): void {
        this.lblUserCoin.string = Num.exchangeToThousands(this.exchangeRate, this._money)
    }

    private showPos: number;
    private hidePos: number;

    protected onLoad(): void {
        // const v = view.getVisibleSize();
        // const s = v.height / 1334;
        // this.node.scale = new Vec3(s, s, 1);
    }

    showPanel(money) {
        // mouse.node.audio.playclickopen();
        this._money = money;

        this.node.active = true;
        this.showPos = (this.panel.h - this.node.parent.h) / 2;
        // this.hidePos = -this.panel.h / 2;

        this.panel.y = this.showPos - this.panel.h;
        tween(this.panel)
            .to(0.2, { y: this.showPos })
            .start()
        tween(this.node)
            .delay(0.2)
            .call(() => {
                tween(this.panel.children[0])
                    .to(0.2, { opacity: 255 })
                    .start()
            })
            .start()
    }

    hidePanel() {
        tween(this.panel)
            .to(0.2, { y: this.showPos - this.panel.h })
            .call(() => {
                this.node.active = false;
            })
            .start()
    }

    onClose() {
        // mouse.node.audio.playclickclose();
        this.hidePanel();
    }
}


