import { _decorator, Component, Node, Label, Button, director } from 'cc';
import { UserMgr } from './UserMgr';
import { UIEnterRoom } from '../ui_enter_room/UIEnterRoom';
import { LobbyMgr } from './LobbyMgr';
const { ccclass, property } = _decorator;

@ccclass('RoomListItem')
export class RoomListItem extends Component {
    @property(Label)
    lblName: Label;

    @property(Label)
    lblRoomId: Label;

    @property(Label)
    lblPlayerNum: Label;

    @property(Label)
    lblSpectatorNum: Label;

    @property(Button)
    btnEnter: Button;

    needPassword:boolean;

    start() {

    }

    onBtnEnterClicked() {
        if(this.needPassword){
            tgx.UIMgr.inst.showUI(UIEnterRoom,(ui:UIEnterRoom)=>{
                ui.setFixedId(this.lblRoomId.string);
            });
        }
        else{
            LobbyMgr.inst.doTryEnterRoom(this.lblRoomId.string);
        }
    }

    update(deltaTime: number) {

    }
}

