import { CCString, Component, Size, Sprite, SpriteFrame, SpriteAtlas, UITransform, _decorator } from "cc";
import { EDITOR } from "cc/env";
import { LanguageData } from "./LanguageData";
import { SubGameMgr } from "db://assets/module_basic/scripts/SubGameMgr";
import { ModuleDef } from "db://assets/scripts/ModuleDef";
import { ResourceMgr } from "db://assets/core_tgx/base/ResourceMgr";

const { ccclass, property, menu } = _decorator;

/** 图片多语言 */
@ccclass("LanguageSprite")
@menu('OopsFramework/Language/LanguageSprite （图片多语言）')
export class LanguageSprite extends Component {
    @property({
        tooltip: "是否为图集"
    })
    private isAtlas: boolean = false;

    @property({ serializable: true })
    private _spAtlasName: string = "";
    @property({ type: CCString, serializable: true, tooltip: "图集名" })
    get spAtlasName(): string {
        return this._spAtlasName || "";
    }
    set spAtlasName(value: string) {
        this._spAtlasName = value;
        if (!EDITOR) {
            this.updateSprite();
        }
    }

    @property({ serializable: true })
    private _dataID: string = "";
    @property({ type: CCString, serializable: true, tooltip: "图片名" })
    get dataID(): string {
        return this._dataID || "";
    }
    set dataID(value: string) {
        this._dataID = value;
        if (!EDITOR) {
            this.updateSprite();
        }
    }

    @property({
        tooltip: "是否设置为图片原始资源大小"
    })
    private isRawSize: boolean = true;

    @property({
        tooltip: "是否是公共包的资源"
    })
    private isBasic: boolean = false;

    start() {
        this.updateSprite();
    }

    /** 更新语言 */
    language() {
        this.updateSprite();
    }

    private updateSprite() {

        let bundle = ModuleDef.BASIC;
        if(!this.isBasic && SubGameMgr.gameMgr && SubGameMgr.gameMgr.gameScene)
        {
            bundle = SubGameMgr.gameMgr.gameScene.bundle;
        }
        // 获取语言标记
        let path = ``;
        if (this.isAtlas) {
            if(this.spAtlasName == "")
            {
                return;
            }
            path = `language/texture/${LanguageData.current}/${this.spAtlasName}`;

            ResourceMgr.inst.loadResByModuleBundle(bundle, path, SpriteAtlas, (err, data) => {
                if (err) {
                    console.error("[LanguageSprite] 资源不存在 " + path + "," + bundle);
                }
                else {
                    let spcomp: Sprite = this.getComponent(Sprite)!;
                    spcomp.spriteFrame = data.getSpriteFrame(this.dataID);
                    /** 修改节点为原始图片资源大小 */
                    if (this.isRawSize) {
                        //@ts-ignore
                        let rawSize = res._originalSize as Size;
                        spcomp.getComponent(UITransform)?.setContentSize(rawSize);
                    }
                }
            });
        } else {
            if(this.dataID == "")
            {
                return;
            }
            path = `language/texture/${LanguageData.current}/${this.dataID}/spriteFrame`;
            ResourceMgr.inst.loadResByModuleBundle(bundle, path, SpriteFrame, (err, data) => {
                if (err) {
                    console.error("[LanguageSprite] 资源不存在 " + path + "," +bundle);
                }
                else {
                    let spcomp: Sprite = this.getComponent(Sprite)!;
                    spcomp.spriteFrame = data;
                    /** 修改节点为原始图片资源大小 */
                    if (this.isRawSize) {
                        //@ts-ignore
                        let rawSize = data._originalSize as Size;
                        spcomp.getComponent(UITransform)?.setContentSize(rawSize);
                    }
                }
            })
        }
    }
}