import { Node, instantiate } from "cc"; // 引入 Cocos Creator 中的 Node 类和 instantiate 实例化函数
import { GameUILayers } from "../../scripts/GameUILayers"; // 引入 UI 层级定义
import { Layout_UIMail } from "./Layout_UIMail"; // 引入邮件 UI 的布局定义
import { MailItem } from "./MailItem"; // 引入邮件项组件
import { lobbyNet } from "../scripts/NetGameServer"; // 引入网络通信模块
import { ModuleDef } from "../../scripts/ModuleDef"; // 模块定义，用于模块化管理
import { LanguageData } from "../scripts/language/LanguageData";

// 声明为 tgx 框架的一个模块（BASIC）
@tgx_class(ModuleDef.BASIC)
export class UIMail extends tgx.UIController {
    private firsttime: boolean = true;
    constructor() {
        // 初始化 UI 控制器：指定资源路径、UI 层级和布局类
        super("ui_mail/ui_mail", GameUILayers.POPUP, Layout_UIMail);
    }

    private _itemPrefab: Node = null; // 邮件项预制体
    private _lastSelected: MailItem = null; // 上一个选中的邮件项

    // 创建 UI 时的回调
    protected onCreated(): void {
        let layout = this._layout as Layout_UIMail;

        // 获取邮件项预制体，并从界面上移除所有已有子项
        this._itemPrefab = layout.mailRoot.children[0];
        layout.mailRoot.removeAllChildren();

        // 关闭按钮点击事件
        this.onButtonEvent(layout.btnClose, () => {
            this.close(); // 关闭当前 UI
        });

        this.onButtonEvent(layout.Read, () => {
            layout.lblContent.node.active = false;
            layout.detail.active = false;
        });

        // “全部标记为已读”按钮点击事件
        this.onButtonEvent(layout.btnMarkAllAsRead, async () => {
            let ret = await lobbyNet.callApi('lobby/mail/MarkAllAsRead', {});
            if (ret.isSucc) {
                this.onMarkAllAsRead(); // 标记本地 UI 状态
            }
        });

        // “清空全部邮件”按钮点击事件
        this.onButtonEvent(layout.btnClearAll, async () => {
            // 弹出提示确认框
            tgx.UIAlert.show(LanguageData.getLangByID("tid_basic_alert_message3"), true).onClick(async b => {
                if (b) {
                    let ret = await lobbyNet.callApi('lobby/mail/ClearAllMails', {});
                    console.log(ret);
                    if (ret.isSucc) {
                        layout.mailRoot.removeAllChildren(); // 清空 UI
                        layout.lblContent.node.active = false; // 隐藏邮件内容
                        layout.detail.active = false;
                        layout.emptyTips.active = true; // 显示无邮件提示
                        this._lastSelected = null;
                    }
                }
            });
        });

        // “删除当前邮件”按钮点击事件
        this.onButtonEvent(layout.btnDelete, () => {
            tgx.UIAlert.show(LanguageData.getLangByID("tid_basic_alert_message2"), true).onClick(async b => {
                if (b) {
                    let deleteItem = this._lastSelected;
                    let ret = await lobbyNet.callApi('lobby/mail/DeleteMail', { mailId: this._lastSelected.data.mailId });
                    if (ret.isSucc) {
                        deleteItem.node.removeFromParent(); // 移除邮件项节点
                        if (!layout.mailRoot.children.length) {
                            // 如果没有邮件了，清空内容区域并显示提示
                            layout.lblContent.node.active = false;
                            layout.detail.active = false;
                            layout.emptyTips.active = true;
                            this._lastSelected = null;
                        } else {
                            // 自动选中第一封邮件
                            this.setAsSelected(layout.mailRoot.children[0].getComponent(MailItem));
                        }
                    }
                }
            });
        });

        this.initMailList(); // 初始化邮件列表
    }

    // 将所有邮件设置为已读状态（本地 UI 表示）
    onMarkAllAsRead() {
        let layout = this._layout as Layout_UIMail;
        layout.mailRoot.children.forEach(v => {
            let comp = v.getComponent(MailItem);
            comp.hasRead.active = false;
            comp.data.state = 'read';
            this._lastSelected.hasRead.active = false;
        });
    }

    // 初始化邮件列表（从服务器获取邮件）
    async initMailList() {
        let layout = this._layout as Layout_UIMail;
        let ret = await lobbyNet.callApi('lobby/mail/GetMails', {});
        ret = {
            "isSucc": true,
            "res": {
                "mails": [
                    {
                        "mailId": "21809",
                        "uid": "33",
                        "from": "system",
                        "time": 1749622571,
                        "title": "欢迎登录",
                        "content": "张盼盼，你好！\n这封邮件会在你每次登录的时候收到。",
                        "state": "unread"
                        
                    },
                    {
                        "mailId": "21781",
                        "uid": "33",
                        "from": "system",
                        "time": 1749610067,
                        "title": "欢迎登录",
                        "content": "张盼盼，你好！\n这封邮件会在你每次登录的时候收到。",
                        "state": "readed"
                    },
                    {
                        "mailId": "5747",
                        "uid": "33",
                        "from": "system",
                        "time": 1742350802,
                        "title": "欢迎登录",
                        "content": "张盼盼，你好！\n这封邮件会在你每次登录的时候收到。",
                        "state": "unread"
                    },
                    {
                        "mailId": "5746",
                        "uid": "33",
                        "from": "system",
                        "time": 1742350751,
                        "title": "欢迎登录",
                        "content": "张盼盼，你好！\n这封邮件会在你每次登录的时候收到。",
                        "state": "unread"
                    }
                ]
            }
        }
        if (!ret.isSucc) {
            layout.emptyTips.active = true;
            return;
        }

        layout.emptyTips.active = false;

        if (!ret.res.mails.length) {
            layout.emptyTips.active = true;
            return;
        }

        // 邮件按时间倒序排列
        ret.res.mails.sort((a, b): number => {
            return b.time - a.time;
        });

        let index = 0;
        ret.res.mails.slice(0, 10).forEach(async v => {
            let item = instantiate(this._itemPrefab); // 实例化邮件项
            layout.mailRoot.addChild(item);
            let comp = item.getComponent(MailItem);
            comp.setData(v); // 设置数据时间
            

            // 邮件点击事件：设置为选中状态
            this.onButtonEvent(item, () => {
                if (this._lastSelected != comp) {
                    this.setAsSelected(comp);
                }
            });
            if(v.state == "readed"){
                comp.hasRead.active = false;
            }
            comp.selected = false;
        
            index++;
        });
        
    }

    // 设置某封邮件为选中状态
    async setAsSelected(comp: MailItem) {
        let layout = this._layout as Layout_UIMail;
        layout.lblContent.node.active = true;
        layout.detail.active = true;
        layout.lblContent.string = comp.data.content + '\n' + new Date(comp.data.time).toString();
        layout.titleLabel.string = comp.data.title;
        comp.selected = true;

        // 取消上一个选中的状态
        if(this.firsttime == false){
            this._lastSelected.selected = false;
        }else{
            this.firsttime = false;
        }
        this._lastSelected = comp;
        // 如果当前邮件还未读，则向服务器标记为已读
        if (comp.data.state == "unread") {
            let ret = await lobbyNet.callApi('lobby/mail/MarkAsRead', { mailId: comp.data.mailId });
            if (ret.isSucc) {
                comp.data.state = 'read';
                comp.hasRead.active = false;
                console.log()
            }
        }
    }
}
