import { _decorator, Component, instantiate, Node, Prefab, Toggle } from 'cc';
import { Layout_UITask } from './Layout_UITask';
import { ModuleDef } from "../../../scripts/ModuleDef";
import { GameUILayers } from '../../../scripts/GameUILayers';

@tgx_class(ModuleDef.BASIC)
export class UITask extends tgx.UIController {
    index: number = 0;

    constructor() {
        super("ui_task/ui_task", GameUILayers.POPUP, Layout_UITask);
    }

    protected async onCreated(): Promise<void> {
        let layout = this._layout as Layout_UITask;
        this.onButtonEvent(layout.btnClose, () => {
            this.close();
        });

        let node = instantiate(layout.frameList[this.index]);
        layout.frame.addChild(node);

        this.onButtonEvent(layout.btnHelp, () => {
            let node = instantiate(layout.canvasList[this.index]);
            layout.canvas.addChild(node);
        });

        this.onToggleEvent(layout.tabs, (checkedItem: Toggle) => {
            this.index = layout.tabs.toggleItems.indexOf(checkedItem);
            if (this.index != -1) {
                layout.frame.removeAllChildren();
                let node = instantiate(layout.frameList[this.index]);
                layout.frame.addChild(node);
            }
        });
    }
}


