import { UITransform } from 'cc';
import { UIOpacity } from 'cc';
import { Vec3 } from 'cc';
import { tween } from 'cc';
import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('AnnouncementMgr')
export class AnnouncementMgr extends Component {
    @property(Node)
    public content:Node ;
    @property(Node)
    public bg:Node ;
    @property(Node)
    public announcementPos:Node ;
    start() {
        this.Loop();
    }

    Loop(){
        const uiOpacity = this.content.getComponent(UIOpacity) || this.content.addComponent(UIOpacity);
        uiOpacity.opacity = 0;

        let contentBox = this.content.getComponent(UITransform).getBoundingBox();
        let anPos = this.announcementPos.getComponent(UITransform).getBoundingBox();
        this.content.position = new Vec3(anPos.x + anPos.width , 0 ,0);
        tween(uiOpacity)
        .to(1, { opacity: 255 })  // 0.5 秒淡入
        .start();
        tween(this.content)
            .to(3, { position: new Vec3(anPos.x - contentBox.width  , 0 ,0) } )
            .call(() => {
                // 移动结束后开始淡出
                tween(uiOpacity)
                    .to(0.5, { opacity: 0 })  // 0.5 秒淡出
                    .call(() => {
                        // 淡出后可以再次执行 Loop
                        this.Loop();
                    })
                    .start();
            })
            .start();
    }

    update(deltaTime: number) {
        
    }
}


