import { PlaySlotSymbol, PlaySlotCoordinate } from "./Slots";

//中奖符号
export interface PlayPaySymbol {
    //符号id
    id: string;
    //此符号出现的所有位置,即为需要消除的符号位置
    points: PlaySlotCoordinate[];
    payout: string;
}

export interface PlaySlotRound {
    symbols: PlaySlotSymbol[];

    //本轮消除的符号位置
    deleteSymbols?: PlaySlotSymbol[];

    //本轮新增的符号
    newSymbols?: PlaySlotSymbol[];

    payout: string;//赢取的钱
    freeCount?: number;//赢得的免费次数
    //本次游戏中奖的所有符号
    paySymbols: PlayPaySymbol[];
    //本轮的乘数
    multipliers: number[];
}
