export interface TongPai {
    userId: string;
    chairId: number;
    cards: number[];
}

export interface UserData {
    userId: string;
    chairId: number;
    userCard: number[];
    playRecords: any;
    score: number;
    bombScore: number;
    badScore: number;
    allScore: number;
    allGold: number;
    frozen: boolean;
    isWinner: boolean;
    isCreator: boolean;
}

export interface UserBigData {
    userId: string;
    chairId: number;
    score: number;
    gold: number;
    winCnt: number;
    bombCnt: number;
    successiveWinCnt: number;
}
//server -> client
//游戏一局结束
export interface MsgGameEnd {
    bigWin: boolean;
    tongPai?: TongPai;
    userData: UserData[];
    bigData?: UserBigData[];
    reason: number;
}
