import { CARD_TYPE_INFO } from "./MsgTurnPlayer";
import { UserCard } from "./MsgGameStart";

export enum GameStatus {
    GAME_STATUS_FREE = 0, //空闲状态
    GAME_STATUS_PLAY = 100, //游戏状态
    GAME_STATUS_WAIT = 200, //等待状态
    GAME_STATUS_END = 300,
}

export interface Player {
    uid: string;
    name: string,
    visualId: number,
    chairId: number;
}

//房间信息， 用户进入房间后（并且收到来自客户端的MsgClientReady消息），服务端发送此消息
export interface MsgRoomInfo {
    roomId: string;
    gameStatus: GameStatus;
    serialNumber: number; //底数
    playerCount: number;

    userCards?: UserCard[];
    handRound: number;

    players: Player[];
    prevCards?: CARD_TYPE_INFO;
    currentUserId?: string;
    currentUser?: number; // 当前出牌玩家的坐位号
    currentLeftTime?: number;//单位秒
}