import { Sprite } from 'cc';
import { SpriteFrame } from 'cc';
import { tween } from 'cc';
import { Label } from 'cc';
import { ProgressBar } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { ThirdPersonCameraCtrl } from 'db://assets/core_tgx/easy_controller/ThirdPersonCameraCtrl';
const { ccclass, property } = _decorator;

/**
 * 累充活动数据接口
 */
export interface AccumulateRechargeData {
    // 当前累充进度（元）
    currentAmount: number;
    
    // 已领取的目标ID列表
    claimedTargetIds: number;
    next:number;
    
    // 活动状态：0-未开始，1-进行中，2-已结束
    status: number;
}


@ccclass('leichongMgr')
export class leichongMgr extends Component {
    @property(ProgressBar)
    progressbar:ProgressBar;
    @property(Node)
    playerPos:Node;
    @property(Label)
    moneyNum:Label;
    @property(Node)
    tag: Node[] = []; 
    @property(SpriteFrame)
    giftstate: SpriteFrame[] = []; 
    private progress:number;
    private rechargeData: AccumulateRechargeData;

    start() {
        this.rechargeData = {
            currentAmount: 7788,//累充金额
            claimedTargetIds: 3,//当前领取进度
            next:4,//下一个领取进度
            status: 1//活动状态 目前没有代码对此读取或修改
        };
        this.progress = (this.rechargeData.claimedTargetIds - 1)/6;//除 6 是因为进度条分成6段 可以看商店累充界面  -1是因为数组下标从0开始
        this.progressbar.progress = this.progress;
        this.tag[this.rechargeData.next -1].getComponent(Sprite).spriteFrame = this.giftstate[0];//奖励图标切换
        this.tag[this.rechargeData.next -1].getChildByName("top").active  = true;
        for(let i = 0 ;i<this.rechargeData.claimedTargetIds;i++){
            this.tag[i].getChildByName("done").active = true;
        }
        this.moneyNum.string = this.rechargeData.currentAmount.toString();
        this.playerPos.position = this.tag[this.rechargeData.claimedTargetIds - 1].position;
    }

    claimReward(){//领取奖励函数
        tween(this.playerPos)//领取动画
        .to(3,{position: this.tag[this.rechargeData.next - 1].position})
        .call(() => {//领取回调，图标从可领取变成不可领取等
            this.tag[this.rechargeData.next -1].getChildByName("top").active  = false;
            this.tag[this.rechargeData.next -1].getComponent(Sprite).spriteFrame = this.giftstate[1];
            this.tag[this.rechargeData.next -1].getChildByName("done").active = true;
        })
        .start();
        tween(this.progressbar).to(3,{progress: this.rechargeData.claimedTargetIds/6}).start();
    }

    update(deltaTime: number) {
        
    }
}


