import { AssetManager, assetManager, director } from "cc";
import { SceneUtil } from "../../core_tgx/base/SceneUtils";
import { GameMgr, GameMode } from "./GameMgr";
import { GameServerAuthParams } from "../shared/types/GameServerAuthParams";
import { RoomMgr } from "./RoomMgr";
import { NetLobbyServer, gameNet, lobbyNet } from "./NetGameServer";
import { getSubGameConf } from "./SubGameDef";
import { LobbyMgr } from "./LobbyMgr";
import { ResStartMatch } from "../shared/protocols/public/lobby/PtlStartMatch";
import LoadingScene from "./LoadingScene";
import { SubGameMessage } from "./SubGameMessage";
import { SceneDef } from "../../scripts/SceneDef";
//import { Language } from "../../module_fish_whale/script/const/enumConst";
import { LanguageData } from 'db://assets/module_basic/scripts/language/LanguageData';
import { LanguageManager } from "./language/Language";

export class SubGameMgr {
    private static _inst: SubGameMgr;
    public static get inst(): SubGameMgr {
        if (!this._inst) {
            this._inst = new SubGameMgr();
        }
        return this._inst;
    }

    private _gameMgrMap: { [key: string]: GameMgr } = {};
    public registerGameMgr(gameMgr: GameMgr) {
        this._gameMgrMap[gameMgr.gameType] = gameMgr;
    }

    public static get curGameType(): string {
        return this.gameMgr?.gameType;
    }

    /**
     * @en gameMgr will be assigned in [sub]GameMgr
     * @zh gameMgr 会在具体的子游戏中被赋值
    */
    public static gameMgr: GameMgr;

    /**
     * 进入子游戏
     */
    public async enterSubGame(gameType: string) {
        let conf = getSubGameConf(gameType);
        if (!conf) {
            return;
        }
        // 提示“正在加载”
        tgx.UIWaiting.show(LanguageData.getLangByID("tid_basic_wait_message5"));
        await tgx.SceneUtil.loadBundleSync(conf.bundle);
        await this.loadBunleLanguage(conf.bundle);
        SubGameMgr.gameMgr = this._gameMgrMap[gameType];
        SubGameMgr.gameMgr.init();
        if (SubGameMgr.gameMgr.loadingScene) {
            await this.subGameLoad();
            await this.subGameLoadCompelte();
            tgx.UIWaiting.hide();
        }
        else {
            if (SubGameMgr.gameMgr.entryScene) {
                //如果有设置子游戏的大厅场景
                await SceneUtil.loadScene(SubGameMgr.gameMgr.entryScene);
                tgx.UIWaiting.hide();
            }
            else {
                //如果没有直接进入子游戏的游戏场景
                SubGameMgr.gameMgr.enterGame();
                tgx.UIWaiting.hide();

            }
        }
        
    }
    /**加载子场景 */
    private async subGameLoad() {
        if (SubGameMgr.gameMgr.loadingScene) {
            await SceneUtil.loadScene(SubGameMgr.gameMgr.loadingScene);
            let currentScene = director.getScene();
            if (currentScene) {
                let nodesWithScript = currentScene.getComponentsInChildren(LoadingScene);
                if (nodesWithScript.length > 0) {
                    const loadingSceneScript = nodesWithScript[0];
                    await loadingSceneScript.preLoadRes();
                }
            }
        }
    }
    /**子游戏加载完成 */
    public async subGameLoadCompelte() {
        if (SubGameMgr.gameMgr.entryScene) {
            //如果有设置子游戏的大厅场景
            await SceneUtil.loadScene(SubGameMgr.gameMgr.entryScene);
            tgx.UIWaiting.hide();
        }
        else {
            //如果没有直接进入子游戏的游戏场景
            SubGameMgr.gameMgr.enterGame();
            tgx.UIWaiting.hide();

        }
    }


    /**
     * 创建房间
     * @param gameType 游戏类型
     * @param enterSubGame  是否进入子游戏
     * @param subType 子类型 比如房间id等（初级房 中级房 高级房）---
     * @returns 
     */
    public async CreateRoom(gameType: string, enterSubGame: boolean = true,subType:string = null): Promise<{ isSucc: boolean, roomParams?: GameServerAuthParams }> {

        tgx.UIWaiting.show();
        let result: boolean = false;
        let ret = await LobbyMgr.inst.rpc_CreateRoom(gameType, "placeholder", "",subType);
        result = ret.isSucc;
        if (result) {
            if (ret.isSucc) {
                let params = ret.res.enterRoomParams;
                if (enterSubGame) {
                    let ret = await SubGameMgr.inst.enterSubGameRoom(params, true);
                    result = ret.isSucc;
                }
                tgx.UIWaiting.hide();
                return { isSucc: result, roomParams: params };
            }
        }
        else {
            // 提示“创建失败”
            console.error("create room failer!");
            tgx.UIAlert.show(LanguageData.getLangByID("tid_basic_alert_message13")).onClick(() => {
                //退到平台
                tgx.SceneUtil.loadScene(SceneDef.LOBBY);
            });
        }
        tgx.UIWaiting.hide();
        return { isSucc: false, };
    }
    /**在游戏中 再次匹配 */
    public async MatchGameAgain(gameType: string,subType:string = null): Promise<{ isSucc: boolean, roomParams?: ResStartMatch }> {

        tgx.UIWaiting.show();
        let leaveRoomRet = await RoomMgr.inst.exitRoomToPlayAgain();
        if (leaveRoomRet.isSucc) {
            gameNet.disconnect(3000, 'normal');

            let autoRet = await LobbyMgr.inst.doAuth(null);
            if (autoRet.isSucc) {
                let matchRoomRet = await this.MatchRoom(gameType,true,subType);
                if (matchRoomRet.isSucc) {
                    tgx.UIWaiting.hide();
                    director.emit("")
                    return { isSucc: true };
                }
                else {
                    // 提示“创建失败”
                    console.error("create room failer!");
                    tgx.UIAlert.show(LanguageData.getLangByID("tid_basic_alert_message13"));
                    tgx.UIWaiting.hide();
                    return { isSucc: false };
                }
            }
            else {
                tgx.UIAlert.show(autoRet.err.message);
                tgx.UIWaiting.hide();
                return { isSucc: false };
            }
        }
        else {
            tgx.UIWaiting.hide();
            return { isSucc: false };
        }
    }
    /**
     * 匹配
     * @param gameType 游戏类型
     * @param enterSubGame  是否进入子游戏
     * @returns 
     */
    public async MatchRoom(gameType: string, enterSubGame: boolean = true,subType:string = null): Promise<{ isSucc: boolean, roomParams?: ResStartMatch }> {

        tgx.UIWaiting.show();
        let result: boolean = false;
        let ret = await LobbyMgr.inst.rpc_MatchRoom(gameType,subType);
        result = ret.isSucc;
        if (result) {
            if (ret.isSucc) {
                let params = ret.res;
                if (enterSubGame) {
                    let enterret = await SubGameMgr.inst.enterSubGameRoom(params, true);
                    result = enterret.isSucc;
                }
                tgx.UIWaiting.hide();
                return { isSucc: result, roomParams: params };
            }
        }
        else {
            var tips:string = "";
            if(ret.err && ret.err.code && ret.err.code == 10004)
            {
                //玩家还在游戏中， 无法匹配新游戏
                tips = LanguageData.getLangByID("tid_basic_alert_message131");
            }else
            {
                tips = LanguageData.getLangByID("tid_basic_alert_message132");
            }
            console.error("match room failer!");
            // 提示“创建失败”
            tgx.UIAlert.show(LanguageData.getLangByID(tips)).onClick(() => {
                //退到平台
                RoomMgr.inst.exitRoomToLobby();
            });
        }
        tgx.UIWaiting.hide();
        return { isSucc: false, };
    }
    // 登录跳转进入子游戏
    public async loginJumpEnterSubGameRoom(params: GameServerAuthParams) {
        let conf = getSubGameConf(params.gameType);
        if (!conf) {
            return { isSucc: false };
        }
        // 提示“正在加载”
        tgx.UIWaiting.show(LanguageData.getLangByID("tid_basic_wait_message5"));
        let loadBundleResult: boolean = await tgx.SceneUtil.loadBundleSync(conf.bundle);
        if (!loadBundleResult) {

            return { isSucc: false };
        }
        await this.loadBunleLanguage(conf.bundle);
        SubGameMgr.gameMgr = this._gameMgrMap[params.gameType];
        SubGameMgr.gameMgr.init();
        if (SubGameMgr.gameMgr.loadingScene) {
            await this.subGameLoad();
        }
        return await this.enterSubGameRoom(params, true);
    }
    /**
     * 进入真正子游戏（不是子游戏大厅）
     * @param params  服务器参数
     * @param isReconnect 是否是登录跳转进来的
     * @param silence 是否隐藏加载UI
     * @returns
     */
    public async enterSubGameRoom(params: GameServerAuthParams, silence: boolean = false): Promise<{ isSucc: boolean, err?: { code?: string, message: string } }> {
        if (!silence) {
            // 提示“正在加载”
            tgx.UIWaiting.show(LanguageData.getLangByID("tid_basic_wait_message5"));
        }
        let conf = getSubGameConf(params.gameType);
        if (!conf) {
            if (!silence) {
                tgx.UIWaiting.hide();
            }
            return { isSucc: false, err: { code: '1', message: 'gameType is not found' } };
        }
        RoomMgr.inst.reset();
        SubGameMgr.gameMgr.reset();
        SubGameMgr.gameMgr.subType = params.subType;

        await gameNet.connectToRoomServer(params);
        let ret = await RoomMgr.inst.enterRoom(params);
        if (ret.isSucc) {
            director.emit(SubGameMessage.EnterSubGameRoomSuccess);
            const currentScene = director.getScene();
            if (currentScene && currentScene.name == SubGameMgr.gameMgr.gameScene.name) {

            }
            else {
                let loadRet = await tgx.SceneUtil.loadScene(SubGameMgr.gameMgr.gameScene);
                if (loadRet && lobbyNet.type != 'http') {
                    (lobbyNet as NetLobbyServer).disconnect(3000, 'normal');
                }
            }

            if (!silence) {
                tgx.UIWaiting.hide();
            }
            return { isSucc: true };
        }
        else {
            if (ret.err.message == 'INVALID_CALL' || ret.err.message == 'not_login') {
                RoomMgr.inst.exitRoom();
            }
            if (!silence) {
                tgx.UIWaiting.hide();
            }
            return { isSucc: false, err: { code: ret.err.code ? ret.err.code.toString() : "1", message: ret.err.message } };
        }

    }
    //返回中央大厅（不是子游戏大厅）
    public exitSubGame() {
        if (SubGameMgr.gameMgr) SubGameMgr.gameMgr.uinit();
        if (SubGameMgr.curGameType) {
            let conf = getSubGameConf(SubGameMgr.curGameType);
            if (!conf) {
                return;
            }
            tgx.SceneUtil.unloadBundle(conf.bundle);
            this.delBundleLanguage(conf.bundle);
            SubGameMgr.gameMgr = null;
        }

    }
    private async loadBunleLanguage(bundleName: string): Promise<boolean>
    {
        return new Promise((resolve, reject) => {
            LanguageManager.ins.addBundle(bundleName, function () {
                return resolve(true);
            });
        });  
    }
    private delBundleLanguage(bundleName: string):void
    {
        LanguageManager.ins.delBundle(bundleName);
    }
}