import { UITransform, isValid, Node, Vec2 } from "cc";
import { GameUILayers } from "../../scripts/GameUILayers";
import { Layout_UIAnnouncement } from "./Layout_UIAnnouncement";
import { ModuleDef } from "../../scripts/ModuleDef";
import { LobbyMgr } from "../scripts/LobbyMgr";
import { SubGameMgr } from "../scripts/SubGameMgr";


@tgx_class(ModuleDef.BASIC)
export class UIAnnouncement extends tgx.UIController {
    constructor() {
        super('ui_announcement/ui_announcement', GameUILayers.HUD, Layout_UIAnnouncement);
    }

    private _maskWidth = 0;
    private _contentWidth = 0;

    private _fadeOutStart = 0.1;
    private _fadeOutRange = 0.05;
    
    protected get layout():Layout_UIAnnouncement{
        return this._layout as Layout_UIAnnouncement;
    }

    protected async onCreated(): Promise<void> {

        let ret = await LobbyMgr.inst.rpc_GetAnnouncement(SubGameMgr.curGameType || 'lobby');
        if (!isValid(this.node)) {
            return;
        }

        let pos = this.layout.maskNode.position;
        this._maskWidth = this.layout.maskNode.getComponent(UITransform).width;
        this.layout.lblContent.node.setPosition(this._maskWidth / 2, pos.y, pos.z);

        if (ret.isSucc) {
            this.layout.lblContent.string = ret.res.content;
        }
        else {
            this.layout.lblContent.string = '获取公告失败，请联系管理员';
        }
        this.layout.lblContent.updateRenderData();
        this._contentWidth = this.layout.lblContent.getComponent(UITransform).width;


        this.setMaterialParams();
    }

    setMaterialParams(){
        this.layout.matAnnouncement.setProperty('fadeOutStart',this._fadeOutStart);
        this.layout.matAnnouncement.setProperty('fadeOutEnd',this._fadeOutStart + this._fadeOutRange);
    }

    setPosition(x: number, y: number,fadeOutStart?:number,fadeOutRange?:number) {
        this.node.setPosition(x, y, 0);

        this._fadeOutStart = fadeOutStart || this._fadeOutStart;
        this._fadeOutRange = fadeOutRange || this._fadeOutRange;
        this.setMaterialParams();
    }

    protected onUpdate(dt: number): void {
        if (this.layout) {
            let pos = this.layout.lblContent.node.position;
            if (this._contentWidth && pos.x + this._contentWidth < -this._maskWidth / 2) {
                this.layout.lblContent.node.setPosition(this._maskWidth / 2, pos.y, pos.z);
            }
            else {
                this.layout.lblContent.node.setPosition(pos.x - dt * 200, pos.y, pos.z);
            }
        }
    }
}