import { Vec3 } from "cc";
import { Component } from "cc";
import { _decorator } from "cc";
import { Num } from "../../core_tgx/framework/util/Num";
import { CCFloat } from "cc";

const { ccclass, property } = _decorator;
@ccclass('bezierMove')
export class bezierMove extends Component {
    /**贝塞尔节点数组 */
    public pArr:Vec3[] = [];
    /**tween t 0-1 实现曲线运动*/
    private _t: number = 0;
    public get t() : number {
        return this._t; 
    }
    public set t(v : number) {
        this._t = v;
        let temp:Vec3;
        if(this.pArr.length == 4){
            temp = Num.threeBezier(this.pArr[0],this.pArr[1],this.pArr[2],this.pArr[3],this._t);
        }
        if(this.pArr.length == 3){
            temp = Num.twoBezier(this.pArr[0],this.pArr[1],this.pArr[2],this._t);
        }
        this.node.position = temp;
    }
}