import { _decorator, Button, Component, Node, Slider, Toggle } from 'cc';
import { LanguageManager } from '../scripts/language/Language';
import { Label } from 'cc';
import { ProgressBar } from 'cc';
const { ccclass, property } = _decorator;


@ccclass('Layout_UISettings')
export class Layout_UISettings extends Component {
    @property(Button)
    btnClose: Button;

    @property(ProgressBar)
    progressBarMusic: ProgressBar;

    @property(ProgressBar)
    progressBarEffect: ProgressBar;
    
    @property(Slider)
    sliderMusic: Slider;

    @property(Slider)
    sliderEffect: Slider;

    @property(Toggle)
    chkFPS: Toggle;

    @property(Button)
    btnSelectLanguage: Button;  //选择语言按钮

    @property(Node)
    btnSelect: Node;

    @property(Label)
    nowLanguage: (Label);
    @property(Label)
    nowLanguageCopy: (Label);
    @property([Label])
    choosenLanguages: Label[] = [];

    

    activeBtnSelect(){
        this.btnSelect.active = true;
        this.nowLanguageCopy.string =  this.nowLanguage.string;
        for(let i = 0;i<LanguageManager.ins.languages.length;i++){
            if(LanguageManager.ins.languages[i] == "zh"){
                this.choosenLanguages[i].string = "中文";
            }else if(LanguageManager.ins.languages[i] == "en"){
                this.choosenLanguages[i].string = "  English";
            }
        }
    }

    start(){
        let cur_language = LanguageManager.ins.current;
        if(cur_language == "zh"){
            this.nowLanguage.string ="中文";
        }
        if(cur_language == "en"){
            this.nowLanguage.string = "English";
        }
        console.log(LanguageManager.ins.languages);
        // this.progressBarMusic.node.on('progress-changed', this.onProgressChanged, this);
        // this.schedule(() => {
        //     this.progressBarMusic.progress = Math.random();
        //     }, 1);
    }

    chooseLanguage(event: TouchEvent, args: any){
         LanguageManager.ins.setLanguageByIndex(args);
         if(args == "0"){
            this.nowLanguage.string = "中文";
            this.nowLanguageCopy.string ="中文";
         }
         if(args == "1"){
            this.nowLanguage.string = "English";
            this.nowLanguageCopy.string ="English";
         }
         this.btnSelect.active = false;
    }

    back(){
         this.btnSelect.active = false;
    }
}


