import { e_room_status } from "./holdem";


export interface i_player_simple {
    /** 玩家id */
    uid: string;
    /** 玩家身上的金币 -1表示未知 */
    gold: number;
    /** 座位 */
    seat: number;
    /** 昵称 */
    nickname: string;
    /** 头像 */
    head: string;
}
//server -> client
export interface MsgRoomHoldem {
    room_id: string;
    timestamp: number;//毫秒
    room_status: e_room_status;
    op_time: number;
    players: i_player_simple[];
    /** 小盲注 */
    sb_bet: number,
    /** 大盲注 */
    bb_bet: number;
}