import { _decorator, Button, Component, instantiate, isValid, Label, Node } from 'cc';
import { ModuleDef } from '../../scripts/ModuleDef';
import { GameUILayers } from '../../scripts/GameUILayers';
import { LobbyMgr } from '../scripts/LobbyMgr';
import { RoomListItem } from '../scripts/RoomListItem';
import { SubGameMgr } from '../scripts/SubGameMgr';
const { ccclass, property } = _decorator;

const PAGE_ITEM_NUM = 7;
const PAGE_REFRESH_TIME = 3;


@ccclass('Layout_UIRoomList')
export class Layout_UIRoomList extends Component {
    @property(Node) contentRoot: Node;

    @property(Node) tips: Node;

    @property(Label) lblPage: Label;

    @property(Button) btnClose:Button;
}

@tgx_class(ModuleDef.BASIC)
export class UIRoomList extends tgx.UIController{
    constructor(){
        super("ui_room_list/ui_room_list",GameUILayers.POPUP,Layout_UIRoomList);
    }

    protected get layout():Layout_UIRoomList{
        return this._layout as Layout_UIRoomList;
    }

    protected onCreated(params?: any): void {
        this.onButtonEvent(this.layout.btnClose,()=>{
            this.close();
        });

        this.layout.contentRoot.active = false;
        this.layout.tips.active = true;
        this._itemPrefab = this.layout.contentRoot.children[0];
        this._itemPrefab.removeFromParent();
        this.updatePageLabelView();
        this.refreshRoomList(this._curPage, PAGE_ITEM_NUM);
     
        this.layout.schedule(() => {
            this.refreshRoomList(this._curPage, PAGE_ITEM_NUM);
        }, PAGE_REFRESH_TIME);
    }

    private _curPage: number = 0;
    private _pageNum: number = 1;

    private _itemPrefab: Node;

    private _waitingData = false;
    private _lastClickTime = Date.now();

    async refreshRoomList(curPage: number, pageItemNum: number) {
        if(this._waitingData == true){
            return;
        }
        this._waitingData = true;
        let ret = await LobbyMgr.inst.rpc_GetRoomList(SubGameMgr.curGameType, curPage, pageItemNum);
        this._waitingData = false;
        if (!ret.isSucc) {
            return;
        }
        if (!isValid(this.node)) {
            return;
        }
        this._curPage = ret.res.curPage;
        this._pageNum = ret.res.pageNum;
        this.updatePageLabelView();

        let rooms = ret.res.rooms;
        if (rooms) {
            this.layout.contentRoot.children.forEach(v => {
                v.active = false;
            });
            this.layout.tips.active = false;
            this.layout.contentRoot.active = true;
            for (let i = 0; i < rooms.length; ++i) {
                let node = this.layout.contentRoot.children[i];
                if (!node) {
                    node = instantiate(this._itemPrefab);
                    this.layout.contentRoot.addChild(node);
                }

                node.active = true;

                let cache = rooms[i];
                let item = node.getComponent(RoomListItem);

                item.lblRoomId.string = cache.displayId;
                item.lblName.string = cache.name;
                item.lblPlayerNum.string = cache.playerNum + '/' + cache.maxPlayerNum;

                let maxSpectatorNum = cache.maxUserNum - cache.maxPlayerNum;
                let spectatorNum = cache.userNum - cache.playerNum;
                item.lblSpectatorNum.string = spectatorNum + '/' + maxSpectatorNum;

                item.needPassword = cache.needPassword;
            }
        }
    }

    updatePageLabelView(){
        if(this._pageNum == 0){
            this.layout.lblPage.string = '0/0';
        }
        else{
            this.layout.lblPage.string = (this._curPage + 1) + '/' + this._pageNum;
        }
    }

    onBtnPrePage() {
        if(this._waitingData || Date.now() - this._lastClickTime < 1000){
            return;
        }
        this._lastClickTime = Date.now();

        this._curPage--;
        if (this._curPage < 0) {
            this._curPage = 0;
        }
        this.refreshRoomList(this._curPage, PAGE_ITEM_NUM);
    }

    onBtnNextPage() {
        if(this._waitingData || Date.now() - this._lastClickTime < 1000){
            return;
        }
        this._lastClickTime = Date.now();

        this._curPage++;
        if (this._curPage >= this._pageNum) {
            this._curPage = this._pageNum - 1;
        }
        this.refreshRoomList(this._curPage, PAGE_ITEM_NUM);
    }
}

