import { Node } from "cc";
import { Prefab } from "cc";
import { NodePool } from "cc";
import { Component } from "cc";
import { _decorator } from "cc";
import { baseSymbol } from "./baseSymbol";
import { Vec3 } from "cc";
import { instantiate } from "cc";
import { tween } from "cc";
import { log } from "cc";
import { Logger } from "db://assets/core_tgx/base/log/Logger";

const { ccclass, property } = _decorator;

@ccclass('baseReel')
/**滚轴基类 脚本挂在mask子节点上，坐标0，0居中。
 * 本节点放置所有symbol，通过批量处理位置信息，实现排版及动画。
 * 各轴划定可见范围（定义为轴展示个数*单个symbol宽、高（视水平、垂直滚动而定），如果出现不同大小symbol以基础大小为准）
 * symbol排布规则：symbol以轴为单元，单向排布。
 * 当头部symbol移出可见区域做回收处理，尾部symbol完全进入可见范围从symbolPoll取新的symbol
 * radomIds为所有可能出现的symbol id 如果出现空挡，请校对下radomIds
*/
export class baseReel extends Component {
    @property({ type: Prefab, tooltip: "symbol预制体" })
    public symbolPb: Prefab = null!;

    /**滚轴排布数据 */
    private _reelDates: reelDate[] = []
    public get reelDates(): reelDate[] {
        return this._reelDates
    }
    public set reelDates(v: reelDate[]) {
        this._reelDates = v;
    }

    /**立即停止 */
    stopImmediately(arr: number[], callBack: Function = () => { }) {
        this.unscheduleAllCallbacks();
        for (let i = 0; i < this.reelDates.length; i++) {
            let r: reelDate = this.reelDates[i];
            r.switch = false;
            let symbols: baseSymbol[] = r.symbols;
            //移除旧symbol
            for (let j = symbols.length - 1; j >= 0; j--) {
                symbols[j].recover();
                symbols.splice(j, 1);
            }
        }
        this.bindResult(arr);
        callBack();
    }

    /**滚轴位置 对应 */
    oneReelPos(r:reelDate):Vec3{
        return r.headPos;
    }

    oneReelSize(r:reelDate):number{
        return r.size*r.num;
    }

    /**滚轴排布数据读取数组起始位置，可用于循环滚动接结果 */
    private _arrIndex: number[];
    public get arrIndex(): number[] {
        if (!this._arrIndex) {
            this._arrIndex = [];
            if (this.reelDates && this.reelDates.length > 0) {
                for (let i = 0; i < this.reelDates.length; i++) {
                    if (i == 0) {
                        this._arrIndex[i] = 0;
                    } else {
                        this._arrIndex[i] = this._arrIndex[i - 1] + this.reelDates[i - 1].num;
                    }
                }
            }
        }
        return this._arrIndex;
    }

    /**单个滚轮结果 */
    reelArr(arr: number[],rIndex:number): number[]{
        let temp:number[] = [...arr];
        return temp.splice(this.arrIndex[rIndex],this.reelDates[rIndex].num);
    }

    bindResult(arr: number[], callBack: Function = () => { }, func: Function = () => { }) {
        for (let i = 0; i < this.reelDates.length; i++) {
            let r: reelDate = this.reelDates[i];
            let num: number = r.num;
            for (let j = 0; j < num; j++) {
                let index: number = this.arrIndex[i] + j;
                let id: number = arr[index];
                let symbol: baseSymbol = this.getSymbol(r);
                symbol.aimBn = true;
                symbol.bind(id, r, this.node, true);
                symbol.doReachAimCallBack = callBack;
                func(symbol, index);
            }
        }
    }

    /**处理单个滚轴 */
    bindResultOne(r: reelDate, arr: number[], callBack: Function = () => { }): baseSymbol[] {
        let num: number = r.num;
        let symbols: baseSymbol[] = [];
        for (let j = 0; j < num; j++) {
            let id: number = arr[this.arrIndex[r.index] + j];
            let symbol: baseSymbol = this.getSymbol(r);
            symbols.push(symbol);
            symbol.aimBn = true;
            symbol.bind(id, r, this.node, true);
            if (j == 0) symbol.doReachAimCallBack = callBack;
        }
        return symbols;
    }

    /**控制滚轴状态 */
    protected update(dt: number): void {
        for (let i = 0; i < this.reelDates.length; i++) {
            if (this.reelDates[i].switch) {
                this.removeUnuse(this.reelDates[i].symbols)
            }
            this.updateReel(this.reelDates[i]);
            this.doMove(this.reelDates[i]);
        }
    }

    /**移出所有无用symbol */
    removeUnuseAll() {
        for (let i = 0; i < this.reelDates.length; i++) {
            this.removeUnuse(this.reelDates[i].symbols)
        }
    }
    /** 剔除回收的symbol*/
    removeUnuse(symbols: baseSymbol[]) {
        let temp: number[] = [];
        for (let i = symbols.length - 1; i >= 0; i--) {
            let symbol: baseSymbol = symbols[i];
            if (!symbol.node.parent) {
                symbols.splice(i, 1);
            } else {
                if (temp.indexOf(symbol.poolId) == -1) {
                    temp.push(symbol.poolId);
                } else {
                    symbols.splice(i, 1);
                }
            }
        }
    }

    /**保持可见范围symbol是满的 */
    updateReel(d: reelDate) {
        if (d.switch) {
            this.addS(d);
            this.addE(d);
        }

    }

    /**头添加 */
    addS(d: reelDate) {
        let dir: number = d.vector > 0 ? 1 : -1;
        let symbols: baseSymbol[] = d.symbols;
        let type: ETYPE = d.type;
        let symbol: baseSymbol = symbols[0];
        if (!symbol) {
            this.unshiftSymbol(d);
            return;
        }
        if (type == ETYPE.H) {
            if (dir == 1) {
                if ((symbol.node.x - symbol.fitW / 2) > d.headPos.x) {
                    this.unshiftSymbol(d);
                    this.addS(d);
                }
            } else {
                if ((symbol.node.x + symbol.fitW / 2) < d.headPos.x) {
                    this.unshiftSymbol(d);
                    this.addS(d);
                }
            }
        } else {
            if (dir == 1) {
                if ((symbol.node.y - symbol.fitH / 2) > d.headPos.y) {
                    this.unshiftSymbol(d);
                    this.addS(d);
                }
            } else {
                if ((symbol.node.y + symbol.fitH / 2) < d.headPos.y) {
                    this.unshiftSymbol(d);
                    this.addS(d);
                }
            }
        }
    }

    unshiftSymbol(d: reelDate, id: number | null = null, setPosBn: boolean = true): baseSymbol {
        let symbol: baseSymbol = this.getSymbol(d);
        id = id == null ? this.radomId() : id;
        symbol.bind(id, d, this.node, false, setPosBn);
        return symbol;
    }

    /**尾添加 */
    addE(d: reelDate) {
        let dir: number = d.vector > 0 ? 1 : -1;
        let symbols: baseSymbol[] = d.symbols;
        let type: ETYPE = d.type;
        let symbol: baseSymbol = symbols[symbols.length - 1];
        if (type == ETYPE.H) {
            if (dir == 1) {
                if (symbol.node.x + symbol.fitW / 2 < d.headPos.x + d.vector) {
                    this.pushSymbol(d);
                    this.addE(d);
                }
            } else {
                if ((symbol.node.x - symbol.fitW / 2) > d.headPos.x + d.vector) {
                    this.pushSymbol(d);
                    this.addE(d);
                }
            }
        } else {
            if (dir == 1) {
                if ((symbol.node.y + symbol.fitH / 2) < d.headPos.y + d.vector) {
                    this.pushSymbol(d);
                    this.addE(d);
                }
            } else {
                if ((symbol.node.y - symbol.fitH / 2) > d.headPos.y + d.vector) {
                    this.pushSymbol(d);
                    this.addE(d);
                }
            }
        }
    }

    pushSymbol(d: reelDate, id: number | null = null, setPosBn: boolean = true): baseSymbol {
        let symbol: baseSymbol = this.getSymbol(d);
        id = id == null ? this.radomId() : id;
        symbol.bind(id, d, this.node, true, setPosBn);
        return symbol;
    }

    protected radomIds: number[] = [];//随机的符号id
    protected excludeId: number[] = [];// excludeId：排除的符号id
    /**随机符号id*/
    protected radomId(): number {
        let tempIds: number[] = [...this.radomIds];
        let excludeId: number[] = [...this.excludeId];
        for (let i = 0; i < excludeId.length; i++) {
            tempIds.splice(tempIds.indexOf(excludeId[i]), 1)
        }
        let index: number = Math.floor(Math.random() * tempIds.length);
        if (index == tempIds.length) {
            index = tempIds.length - 1;
        }
        let id: number = tempIds[index];
        return id;
    }

    protected count: number = 0;
    protected getSymbol(d: reelDate): baseSymbol {
        let pbNode: Node = d.pool.get();
        let symbol: baseSymbol
        if (!pbNode) {
            pbNode = instantiate(this.symbolPb);
            symbol = pbNode.getComponent(baseSymbol);
            symbol.poolId = this.getPoolId();
            this.count++;
            // Logger.error("生成symbol总个数：" + this.count);
        } else {
            symbol = pbNode.getComponent(baseSymbol);
        }
        return symbol;
    }

    protected MAXSYMBOL: number = 500;
    private _poolId: number = 0;
    /** 获取请求唯一编号 */
    protected getPoolId() {
        if (this._poolId == this.MAXSYMBOL) this._poolId = 1;
        this._poolId++;
        return this._poolId;
    }

    /**速度 */
    protected v: number = 20;
    /**所有滚轴一起转 */
    protected move() {
        for (let i = 0; i < this.reelDates.length; i++) {
            let r: reelDate = this.reelDates[i];
            let symbols: baseSymbol[] = r.symbols;
            for (let i = 0; i < symbols.length; i++) {
                let symbol: baseSymbol = r.symbols[i];
                symbol.aimBn = false;
            }
            r.switch = true;
        }
    }

    /**转动单个滚轴 */
    protected moveOne(r: reelDate) {
        let symbols: baseSymbol[] = r.symbols;
        for (let i = 0; i < symbols.length; i++) {
            let symbol: baseSymbol = r.symbols[i];
            symbol.aimBn = false;
        }
        r.switch = true;
    }

    protected doMove(r: reelDate) {
        if (r.switch == false) return;
        let moveDir: number = r.vector > 0 ? -1 : 1;
        let symbols: baseSymbol[] = r.symbols;
        for (let i = 0; i < symbols.length; i++) {
            let symbol: baseSymbol = r.symbols[i];
            if (r.type == ETYPE.H) {
                symbol.node.x += moveDir * this.v;
            } else {
                symbol.node.y += moveDir * this.v;
            }
        }
    }

    /**滚动完成移除所有随机产生的symbol */
    removeRadom() {
        for (let i = 0; i < this.reelDates.length; i++) {
            let r: reelDate = this.reelDates[i];
            let symbols: baseSymbol[] = r.symbols;
            for (let j = symbols.length - 1; j >= 0; j--) {
                let symbol: baseSymbol = symbols[j];
                if (symbol.aimBn == false) {
                    symbol.recover();
                    symbols.splice(j, 1);
                }
            }
        }
    }

    /**回弹动画
      * startBn：是否为开始的回弹否则为结束
      */
    protected reboundDis: number = 20;//回弹距离
    protected reboundT: number = 0.2;//回弹时间
    protected rebound(r: reelDate, startBn: boolean, callback: Function = () => { }) {
        let dir: number = r.vector > 0 ? 1 : -1;
        let dis: number = this.reboundDis;
        let t: number = this.reboundT;
        let addSymbol: baseSymbol;
        if (startBn) {
            addSymbol = this.unshiftSymbol(r);
        } else {
            addSymbol = this.pushSymbol(r);
        }
        let symbols: baseSymbol[] = r.symbols;
        for (let j = 0; j < symbols.length; j++) {
            let symbol: baseSymbol = symbols[j];
            symbol.rebound(startBn, this.reboundT, this.reboundDis);
        }
        this.scheduleOnce(() => {
            addSymbol.recover();
            callback();
        }, 2 * this.reboundT)
    }

    getSymbolByIndex(index: number): baseSymbol {
        let arr = this.getSymbols;
        return arr[index];
    }

    get getSymbols(): baseSymbol[] {
        let arr = [];
        for (let i = 0; i < this.reelDates.length; i++) {
            let r: reelDate = this.reelDates[i];
            for (let j = 0; j < r.num; j++) {
                let symbol: baseSymbol = r.symbols[j];
                if (symbol && symbol.aimBn) arr.push(symbol);
            }
        }
        return arr;
    }

    clear() {
        for (let i = 0; i < this.reelDates.length; i++) {
            let r: reelDate = this.reelDates[i];
            let symbols: baseSymbol[] = r.symbols;
            for (let j = 0; j < symbols.length; j++) {
                symbols[j].recover();
            }
            r.pool.clear();
            r.symbols = [];
        }
    }
}

export enum ETYPE {
    H,  //水平
    V   //垂直
}

export interface reelDate {
    index: number;                      //滚轴位于数组的位置，可用做唯一标识
    type: ETYPE;                        //类型
    num: number;                        //子节点个数
    pool: NodePool;                     //对象池
    size: number;                       //大小ETYPE.H取轴高 ETYPE.V取轴宽
    headPos: Vec3;                      //头位置
    vector: number;                     //排布方向（与运动方向相反），正负带有方向 下到上，左到右为正。上到下，右到左为负
    symbols: baseSymbol[];              //存放符号
    switch: boolean;                    //转动开关
    hierarchy?: boolean;                  //层级 true：symbols index越大层级越低 false：symbols index越大层级越高
}