import { GameUILayers } from "../../scripts/GameUILayers";
import { ModuleDef } from "../../scripts/ModuleDef";
import { LobbyMgr } from "../scripts/LobbyMgr";
import { UserMgr } from "../scripts/UserMgr";
import { Layout_UIdailycheck } from "./Layout_UIdailycheck";
import { SubGameMgr } from "../scripts/SubGameMgr";
import { LanguageData } from "../scripts/language/LanguageData";
import { ProgressBar } from "cc";
import { Num } from "../../core_tgx/framework/util/Num";


interface UIDailyCheckData {//后期根据具体需求调整
    // 显示文本
    Now_week:number;                   // 1,2,3,4,5,6,7
    Now_statu:boolean;                 // Yes or No (今日签到否)
    signStatusText: string;            // 签到状态文本（如"已签到"、"未签到"）
    checkTag:number;                   // 已经获得的连续签到点
    consecutiveDaysText: number;       // 连续签到天数文本（如"连续3天"）最多30天
    rewardDescription: string;         // 奖励描述文本
    checked_days:number[];
    
    // 按钮状态
    signButtonEnabled: boolean;        // 签到按钮是否可点击
    claimButtonEnabled: boolean;       // 领取按钮是否可点击
    missedSignButtonEnabled: boolean;  // 补签按钮是否可点击
    
    // 视觉元素
    rewardIcon: string;                // 奖励图标路径
    daysProgress: number;              // 签到进度（0-1）
    nextRewardDate: string;            // 下一次奖励日期
    
    // 交互回调
    onSign: () => void;                 // 签到回调
    onClaim: () => void;                // 领取回调
    onMissedSign: () => void;           // 补签回调
}

@tgx_class(ModuleDef.BASIC)
export class UIdailycheck extends tgx.UIController {
    public dailyData: UIDailyCheckData;
    private static _instance: UIdailycheck = null;
    constructor() {
        super('ui_dailycheck/ui_dailycheck', GameUILayers.POPUP, Layout_UIdailycheck);
        UIdailycheck._instance = this; // 初始化时保存实例
        this.dailyData = {
            Now_week: 3,
            Now_statu: false,
            signStatusText: "",
            consecutiveDaysText: 13,
            checkTag:4,
            rewardDescription: "",
            checked_days:[1,2],
            signButtonEnabled: false,
            claimButtonEnabled: false,
            missedSignButtonEnabled: false,
            rewardIcon: "",
            daysProgress: 0,
            nextRewardDate: "",
            onSign: () => {},
            onClaim: () => {},
            onMissedSign: () => {}
        };
    }

    public static getInstance(): UIdailycheck {
        return this._instance;
    }

    
    protected onCreated(): void {
        let layout = this._layout as Layout_UIdailycheck;
        this.onButtonEvent(layout.btnClose, () => {
            this.close(); //绑定关闭按钮
        });
        for(let i = 0;i < 7; i++){
            layout.dailyButtons[i].interactable = false;// 7天签到按钮初始化（默认交互关闭）
        }
        layout.dailyButtons[this.dailyData.Now_week-1].interactable = true; //使当天签到按钮可交互
        let posx = this.dailyData.consecutiveDaysText/30; //初始化进度长度，等于连续签到日期除以当月总日期
        layout.processbar.getComponent(ProgressBar).progress = posx;//进度条初始化
        

        // this.onButtonEvent(layout.btnCreate, this.onBtnCreateClicked, this);

        // layout.edtName.string = this.getDefaultRoomName();
    }

    private initDailyCheckBtn()
    {

    }

}