import { Node } from "cc";
import { NodePool } from "cc";
import { Component } from "cc";
import { _decorator } from "cc";
import { ETYPE, reelDate } from "./baseReel";
import { Vec3 } from "cc";
import { sp } from "cc";
import { Sprite } from "cc";
import { SpriteFrame } from "cc";
import { Logger } from "db://assets/core_tgx/base/log/Logger";
import { tween } from "cc";
import { EventTouch } from "cc";
import { Button } from "cc";

const { ccclass, property } = _decorator;

@ccclass('baseSymbol')
/**符号基类 
 * scale：1为基准依据滚轴宽度做缩放
*/
export class baseSymbol extends Component {
    @property({ type: sp.Skeleton, tooltip: "龙骨" })
    public ske: sp.Skeleton = null!;
    @property({ type: Sprite, tooltip: "icon" })
    public icon: Sprite = null!;
    @property({ type: Sprite, tooltip: "模糊" })
    public blur: Sprite = null!;
    @property({ type: [sp.SkeletonData], tooltip: "龙骨[]" })
    public skes: sp.SkeletonData[] = [];
    @property({ type: [SpriteFrame], tooltip: "icon[]" })
    public icons: SpriteFrame[] = [];
    @property({ type: [SpriteFrame], tooltip: "模糊[]" })
    public blurs: SpriteFrame[] = [];
    @property({ type: Node, tooltip: "暗" })
    public dark: Node = null!;

    private get pool(): NodePool {
        if (!this.reelDate) {
            Logger.error("未绑定reelDate");
            return;
        }
        return this.reelDate.pool;
    }

    public reelDate: reelDate;
    private _poolId: number = 0;
    public get poolId(): number {
        return this._poolId
    }
    public set poolId(v: number) {
        this._poolId = v;
    }


    /**绑定pool */
    bind(id: number, r: reelDate, parent: Node, pushBn: boolean, setPosBn: boolean = true) {
        this.symbolId = id;
        this.reelDate = r;
        this.node.parent = parent;
        this.fitSize();
        if (pushBn) {
            r.symbols.push(this);
            if (r.hierarchy) {
                this.node.setSiblingIndex(0);
            }
        } else {
            r.symbols.unshift(this);
            let symbols: baseSymbol[] = this.reelDate.symbols;
            let index: number = this.index;
            let next: baseSymbol = index == symbols.length - 1 ? null : symbols[index + 1];//后继
            if (next && !r.hierarchy) {
                this.node.setSiblingIndex(next.node.getSiblingIndex());
            }

        }
        if (setPosBn) this.setPostion();
    }

    /**是否存在目标，目标为headPos */
    private _aimBn: boolean = false;
    public get aimBn(): boolean {
        return this._aimBn
    }
    public set aimBn(v: boolean) {
        this._aimBn = v;
    }

    /**回收 */
    recover() {
        if (!this.pool) {
            Logger.error("未绑定pool");
            return;
        }

        this.pool.put(this.node);
    }

    unuse() {
        this.bindAimPos(this.reelDate.num);
        this.aimBn = false;
        this.doReachAimCallBack = () => { };
    }

    reuse() {
        this.symbolId = 0;
    }

    get index() {
        if (!this.reelDate) {
            Logger.error("未绑定reelDate");
            return;
        }
        let symbols: baseSymbol[] = this.reelDate.symbols;
        for (let i = 0; i < symbols.length; i++) {
            let symbol: baseSymbol = symbols[i];
            if (symbol.poolId == this.poolId) {
                return i;
            }
        }
        return -1;
    }


    public get reelType(): ETYPE {
        return this.reelDate.type;
    }

    public get reelSize(): number {
        return this.reelDate.size;
    }

    private fitScale: number;
    /**适配大小*/
    public fitSize() {
        let nodeSize: number = this.reelType == ETYPE.H ? this.node.h : this.node.w;
        this.fitScale = this.reelSize / nodeSize;
        // this.node.scale = new Vec3(this.fitScale, this.fitScale);
        this.node.w = this.node.w * this.fitScale;
        this.node.h = this.node.h * this.fitScale;
        for (let i = 0; i < this.node.children.length; i++) {
            let child: Node = this.node.children[i];
            child.scale = new Vec3(child.scale.x * this.fitScale, child.scale.y * this.fitScale);
        }
    }
    public get fitW(): number {
        // return this.node.w * this.fitScale;
        return this.node.w;
    }
    public get fitH(): number {
        // return this.node.h * this.fitScale;
        return this.node.h;
    }

    /**设置位置 */
    public setPostion() {
        let symbols: baseSymbol[] = this.reelDate.symbols;
        let index: number = this.index;
        let pre: baseSymbol = this.pre;//前驱
        let next: baseSymbol = index == symbols.length - 1 ? null : symbols[index + 1];//后继
        let dir: number = this.reelDate.vector > 0 ? 1 : -1;
        if (pre) {
            if (this.reelType == ETYPE.H) {
                this.node.position = new Vec3(pre.node.x + (pre.fitW + this.fitW) / 2 * dir, this.reelDate.headPos.y);
            } else {
                this.node.position = new Vec3(this.reelDate.headPos.x, pre.node.y + (pre.fitH + this.fitH) / 2 * dir);
            }
        } else {
            if (next) {
                if (this.reelType == ETYPE.H) {
                    this.node.position = new Vec3(next.node.x - (next.fitW + this.fitW) / 2 * dir, this.reelDate.headPos.y);
                } else {
                    this.node.position = new Vec3(this.reelDate.headPos.x, next.node.y - (next.fitH + this.fitH) / 2 * dir);
                }
            } else {
                if (this.reelType == ETYPE.H) {
                    this.node.position = new Vec3(this.reelDate.headPos.x + this.fitW / 2 * dir, this.reelDate.headPos.y);
                }
                else {
                    this.node.position = new Vec3(this.reelDate.headPos.x, this.reelDate.headPos.y + this.fitH / 2 * dir);
                }
            }
        }
    }

    private get pre(): baseSymbol {
        let symbols: baseSymbol[] = this.reelDate.symbols;
        let index: number = this.index;
        for (let i = index - 1; i >= 0; i--) {
            let symbol: baseSymbol = symbols[i];
            if (symbol.node.parent) {
                return symbol;
            }
        }
        return null;
    }

    protected update(dt: number): void {
        if (!this.reelDate) return;
        if (this.reelDate) {
            this.blurBn = this.reelDate.switch;
        }
        if (this.reelDate.switch == false) return;
        this.removeUnShow();
        this.reachAim();
    }

    /**超出显示范围则移出并回收 */
    removeUnShow() {
        if (!this.node.parent || this.aimBn) return;
        let showBn: boolean = this.showBn();
        if (showBn == false) {
            this.recover();
        }
    }

    /**可见边界 */
    private get borderOne(): number {
        return this.reelType == ETYPE.H ? this.reelDate.headPos.x : this.reelDate.headPos.y;
    }
    private get borderTwo(): number {
        return this.reelType == ETYPE.H ? this.reelDate.headPos.x + this.reelDate.vector : this.reelDate.headPos.y + this.reelDate.vector;
    }

    /**判断是否可见 */
    private showBn(): boolean {
        let borders: number[] = this.reelDate.vector > 0 ? [this.borderOne, this.borderTwo] : [this.borderTwo, this.borderOne];
        if (this.reelType == ETYPE.H) {
            if ((this.node.x + this.fitW / 2) >= borders[0] && (this.node.x - this.fitW / 2) <= borders[1]) {
                return true;
            }
        } else {
            if ((this.node.y + this.fitH / 2) >= borders[0] && (this.node.y - this.fitH / 2) <= borders[1]) {
                return true;
            }
        }
        return false;
    }

    /**有目标则判断是否达成 */
    protected reachAim() {
        if (this.aimBn == false) return;
        let dir: number = this.reelDate.vector > 0 ? 1 : -1;
        let r: reelDate = this.reelDate;
        if (this.reelDate.type == ETYPE.H) {
            if (dir == 1) {
                if (this.node.x - this.fitW / 2 <= r.headPos.x) {
                    this.doReachAim();
                }
            } else {
                if (this.node.x + this.fitW / 2 >= r.headPos.x) {
                    this.doReachAim();
                }
            }
        } else {
            if (dir == 1) {
                if (this.node.y - this.fitH / 2 <= r.headPos.y) {
                    this.doReachAim();
                }
            } else {
                if (this.node.y + this.fitH / 2 >= r.headPos.y) {
                    this.doReachAim();
                }
            }
        }
    }

    bindAimPos(index: number) {
        let dir: number = this.reelDate.vector > 0 ? 1 : -1;
        let r: reelDate = this.reelDate;
        if (this.reelDate.type == ETYPE.H) {
            this.node.x = r.headPos.x + (this.fitW / 2 + this.fitW * index) * dir;
        } else {
            this.node.y = r.headPos.y + (this.fitH / 2 + this.fitH * index) * dir;
        }
    }

    get aimPos(): number {
        let dir: number = this.reelDate.vector > 0 ? 1 : -1;
        let r: reelDate = this.reelDate;
        if (this.reelDate.type == ETYPE.H) {
            return r.headPos.x + (this.fitW / 2 + this.fitW * this.index) * dir;
        } else {
            return r.headPos.y + (this.fitH / 2 + this.fitH * this.index) * dir;
        }
    }

    public doReachAimCallBack: Function = () => { };
    protected doReachAim() {
        this.reelDate.switch = false;
        let symbols: baseSymbol[] = this.reelDate.symbols;
        let count: number = 0;
        for (let i = 0; i < symbols.length; i++) {
            if (symbols[i].aimBn == false) {
                symbols[i].recover();
            } else {
                symbols[i].bindAimPos(count);
                count++;
            }
        }
        this.doReachAimCallBack();
    }

    rebound(startBn: boolean, t: number, dis: number) {
        let node: Node = this.node
        let r: reelDate = this.reelDate;
        let dir: number = r.vector > 0 ? 1 : -1;
        if (r.type == ETYPE.H) {
            if (startBn) {
                tween(node)
                    .by(t, { x: dis * dir }, { easing: 'quartOut' })
                    .by(t, { x: -dis * dir }, { easing: 'quartIn' })
                    .start();
            } else {
                tween(node)
                    .by(t, { x: -dis * dir }, { easing: 'quartOut' })
                    .by(t, { x: dis * dir }, { easing: 'quartIn' })
                    .start();
            }
        } else {
            if (startBn) {
                tween(node)
                    .by(t, { y: dis * dir }, { easing: 'quartOut' })
                    .by(t, { y: -dis * dir }, { easing: 'quartIn' })
                    .start();
            } else {
                tween(node)
                    .by(t, { y: -dis * dir }, { easing: 'quartOut' })
                    .by(t, { y: dis * dir }, { easing: 'quartIn' })
                    .start();
            }
        }
    }

    protected init() {
        this.showType = ESHOWTYPE.normal;
        this.blurBn = false;
        this.darkBn = false;
        if (this.node.getComponent(Button)) this.node.getComponent(Button).interactable = false;
    }

    private _darkBn: boolean = false;
    public get darkBn(): boolean {
        return this._darkBn;
    }
    public set darkBn(v: boolean) {
        this._darkBn = v;
        if (this.dark) this.dark.active = v;
    }

    private _blurBn: boolean = false;
    public get blurBn(): boolean {
        return this._blurBn
    }
    public set blurBn(v: boolean) {
        this._blurBn = v;
        if (this.blur) this.blur.node.active = v;
        if (v) {
            this.icon.node.active = false;
            this.ske.node.active = false;
        } else {
            if (this.showType == ESHOWTYPE.normal) {
                this.icon.node.active = true;
            } else {
                this.ske.node.active = true;
            }
        }
    }

    private _showType: ESHOWTYPE = ESHOWTYPE.normal;
    public get showType(): ESHOWTYPE {
        return this._showType;
    }
    public set showType(v: ESHOWTYPE) {
        this._showType = v;
        this.ske.node.active = v == ESHOWTYPE.ske;
        this.icon.node.active = v == ESHOWTYPE.normal;
    }

    private _symbolId: number = 0;
    public get symbolId(): number {
        return this._symbolId;
    }
    public set symbolId(v: number) {
        this._symbolId = v;
        let index: number = v - 1;
        if (v == 0) {
            this.ske.skeletonData = undefined;
            this.icon.spriteFrame = undefined;
            if (this.blur) this.blur.spriteFrame = undefined;
            for (let i = 0; i < this.node.children.length; i++) {
                this.node.children[i].active = false;
            }
        } else {
            this.ske.skeletonData = this.skes[index];
            this.icon.spriteFrame = this.icons[index];
            if (this.blur) this.blur.spriteFrame = this.blurs[index];
        }
        this.init();
    }

    /**播放动画 */
    protected playAni(aimStr: string, loopBn: boolean = true) {
        if (this.showType != ESHOWTYPE.ske) return;
        this.ske.setAnimation(0, aimStr, loopBn);
    }
    /**添加动画 */
    protected addAni(aimStr: string, loopBn: boolean = true) {
        if (this.showType != ESHOWTYPE.ske) return;
        this.ske.addAnimation(0, aimStr, loopBn);
    }
}

export enum ESHOWTYPE {
    normal,
    ske
}