import { EditBox, Toggle } from "cc";
import { GameUILayers } from "../../scripts/GameUILayers";
import { UserMgr } from "../scripts/UserMgr";
import { UserInfo } from "../shared/types/UserInfo";
import { Layout_UIUserInfo } from "./Layout_UIUserInfo";
import { ModuleDef } from "../../scripts/ModuleDef";
import { LanguageData } from "../scripts/language/LanguageData";
import { ConfigMgr } from "../scripts/ConfigMgr";
import { instantiate } from "cc";
import { director } from "cc";
import { HeadItem } from "./headItem";
import { Node } from "cc";

@tgx_class(ModuleDef.BASIC)
export class UIUserInfo extends tgx.UIController {
    constructor() {
        super("ui_user_info/ui_user_info", GameUILayers.POPUP, Layout_UIUserInfo);
    }

    private _userInfo: UserInfo;
    private _gender: number = undefined;
    private _introduction: string = undefined;
    private _visualId: number = undefined;

    protected async onCreated(userInfo: UserInfo) {
        this._userInfo = userInfo;

        // let config = await UserMgr.inst.getBasicConfig();
        // if (!config) {
        //     // 提示“获取匹配失败，请稍后再试”
        //     tgx.UIAlert.show(LanguageData.getLangByID("tid_basic_alert_message22"));
        //     this.close();
        //     return;
        // }
        let layout = this._layout as Layout_UIUserInfo;
        this.onButtonEvent(layout.btnClose, async () => {
            if (this._gender != undefined || this._introduction != undefined || this._visualId != undefined) {
                let ret = await UserMgr.inst.rpc_ModifyUserInfo(this._gender, this._introduction,this._visualId);
                if (ret.isSucc) {
                    // 提示“用户信息修改成功！”
                    tgx.UIAlert.show(LanguageData.getLangByID("tid_basic_alert_message26"));
                }
                else {
                    // 提示“用户信息修改失败！”
                    tgx.UIAlert.show(LanguageData.getLangByID("tid_basic_alert_message27"));
                }
            }
            else {
                this.close();
            }
            this.close();
        });

        this.onToggleEvent(layout.toggleGenderOptions, (v: Toggle) => {
            let index = layout.toggleGenderOptions.toggleItems.indexOf(v);
            if (index != this._userInfo.gender) {
                this._gender = index;
            }
        });

        this.initData();
        director.on("onHeadClicked", this.onHeadClicked, this);
    }

    protected onDispose(): void {
        let layout = this._layout as Layout_UIUserInfo;
        director.off("onHeadClicked", this.onHeadClicked, this);
        UserMgr.inst.releaseHeadIcon(layout.sprHeadImg);
    }

    initData() {
        if (!this.node) {
            return;
        }
        let layout = this._layout as Layout_UIUserInfo;

        let bShow = !!this._userInfo;
        layout.lblName.node.active = bShow;
        layout.lblId.node.active = bShow;
        layout.lblGender.node.active = bShow;
        layout.toggleGenderOptions.node.active = bShow;

        if (!this._userInfo) {
            return;
        }

        UserMgr.inst.setUserIconAndName(this._userInfo.uid, layout.sprHeadImg);
        layout.lblName.string = this._userInfo.name;    // 昵称
        layout.lblId.string = this._userInfo.uid;      // ID
        //layout.lblGender.string = '性别：';
        let gender = this._userInfo.gender || 0;
       
        layout.toggleGenderOptions.toggleItems[gender].isChecked = true;
        this.initHead();

    }
    initHead()
    {
        var headConfig:any[] = ConfigMgr.inst.getHeadConfig();
        let layout = this._layout as Layout_UIUserInfo;
        for(var i:number = 0; i < headConfig.length; i++)
        {
            var headData:any = headConfig[i];
            var prefabInstance = instantiate(layout.headItem);
            prefabInstance.getComponent(HeadItem).initData(headData);
            layout.heads.addChild(prefabInstance);
        }
    }
    private async onHeadClicked(data:any)
    {
        if(data.id != this._userInfo.visualId)
        {
            this._visualId = data.id;
        }
        
        let layout = this._layout as Layout_UIUserInfo;
        var headNodes:Node[] = layout.heads.children;
        for(var i:number = 0; i < headNodes.length; i++)
        {
            headNodes[i].getComponent(HeadItem).updateSeclet(data.id);
        }
        UserMgr.inst.loadHeadIcon(data.id, layout.sprHeadImg);
        
    }
}