import { GameUILayers } from "../../scripts/GameUILayers";
import { ModuleDef } from "../../scripts/ModuleDef";
import { LanguageData } from "../scripts/language/LanguageData";
import { LobbyMgr } from "../scripts/LobbyMgr";
import { SubGameMgr } from "../scripts/SubGameMgr";
import { UserMgr } from "../scripts/UserMgr";
import { Layout_SearchingRival } from "./Layout_SearchingRival";

@tgx_class(ModuleDef.BASIC)
export class UI_SearchingRival extends tgx.UIController {

    constructor() {
        super('ui_searching_rival/ui_searching_rival', GameUILayers.POPUP, Layout_SearchingRival);
    }

    private _startTime = Date.now();
    private _state = '';
    protected onCreated(): void {
        let layout = this._layout as Layout_SearchingRival;
        this.onButtonEvent(layout.btnCancel, async () => {
            if(this._state == 'matching'){
                let ret = await LobbyMgr.inst.rpc_QuickPlayCancel();
                if (ret.isSucc) {
                    this._state = 'cancelled';
                    this.close();
                }
            }
            else{
                this._state = 'cancelled';
                this.close();
            }
            
        });
    }

    protected onUpdate(dt: number): void {
        let layout = this._layout as Layout_SearchingRival;
        let elapsedTime = Math.floor((Date.now() - this._startTime) / 1000);
        if (elapsedTime < 60) {
            layout.lblTime.string = elapsedTime + 's';
        }
        else {
            let s = elapsedTime % 60;
            let m = Math.floor(elapsedTime / 60);
            layout.lblTime.string = m + 'm' + s + 's';
        }
    }

    async startMatch(type: string) {
        if(this._state){
            return;
        }
        this._state = 'matching';
        let ret = await LobbyMgr.inst.rpc_QuickPlay(type);
        this.close();
        if (!ret.isSucc) {
            // 提示“未找到合适的对手”
            tgx.UIAlert.show(LanguageData.getLangByID("tid_basic_alert_message4"));
        }
        else {
            let params = ret.res;
            return await SubGameMgr.inst.enterSubGameRoom(params);
        }
    }
}