import { _decorator, Component, log, Node, Toggle } from 'cc';
import { GameUILayers } from "../../scripts/GameUILayers";
import { Layout_UILanguage } from "./Layout_UILanguage";
import { ModuleDef } from "../../scripts/ModuleDef";
import { LanguageManager } from '../scripts/language/Language';

const { ccclass, property } = _decorator;

//设置语言界面
@tgx_class(ModuleDef.BASIC)
export class UILanguage extends tgx.UIController {
    private languageIndex : number = 0;//当前是第几个语言 


    constructor()
    {
        super("ui_language/ui_language", GameUILayers.POPUP, Layout_UILanguage);
    }

    protected onCreated(): void {
        let layout = this._layout as Layout_UILanguage;

        //关闭按钮
        this.onButtonEvent(layout.btnClose, () => {
            this.close();
        });

        //取消按钮
        this.onButtonEvent(layout.btnCancel, () => {
            this.close();
        });

        //选中某种语言
        this.onToggleEvent(layout.toggleLanguage, (v: Toggle) => {
            //选中第几个语言
            this.languageIndex = layout.toggleLanguage.toggleItems.indexOf(v);
                    
        });

        //选择语言确定按钮
        this.onButtonEvent(layout.btnLanguage, () => {
            log(this.languageIndex);
            //切换语言
            LanguageManager.ins.setLanguageByIndex(this.languageIndex);
            this.close();
        });

        //显示当前使用的语言
        this.languageIndex = LanguageManager.ins.index;
        layout.toggleLanguage.toggleItems[this.languageIndex].isChecked = true;
    }
}


