import { Node } from "cc";
import { GameUILayers } from "../../scripts/GameUILayers";
import { Layout_UIHistory } from "./Layout_UIHistory";
import { Button } from "cc";
import { Sprite } from "cc";
import { instantiate } from "cc";
import { Vec3 } from "cc";
import { Layout_UIHistoryItem } from "./Layout_UIHistoryItem";
import { Layout_UIHistoryOptionItem } from "./Layout_UIHistoryOptionItem";
import { subGameConf, SubGameType } from "../scripts/SubGameDef";
import { Layout_UIHistoryDetails } from "./Layout_UIHistoryDetails";
import { Layout_UIHistoryDetailsItem } from "./Layout_UIHistoryDetailsItem";
import { apiNet } from "../scripts/NetGameServer";
export class UIHistoryDetails extends tgx.UIController {

    private _data:any;
    private _totalPage:number = 0;
    private _curPage:number = 0;
    private _pageSize:number = 10;
    private _resourceData:any;
    private _utc:number;
    constructor() {
        super("prefabs/history/historydetailsview", GameUILayers.POPUP, Layout_UIHistoryDetails);
    }
    protected onCreated(params?:any) {
        this._utc = new Date().getTimezoneOffset();
        this._resourceData = params;
        this.onBindData(null);
        let layout = this._layout as Layout_UIHistoryDetails;
        layout.btnClose.node.on(Button.EventType.CLICK, this.onClickClose,this);
        layout.btn_pre.node.on(Button.EventType.CLICK, this.onClickPrePage,this);
        layout.btn_next.node.on(Button.EventType.CLICK, this.onClickNextPage,this);
        this.requestHistoryList();
    }



    protected onDispose(): void {
        
    }

    private onBindData(data: any) {
        this._data = data;
        let layout = this._layout as Layout_UIHistoryDetails;
        if(this._data == null)
        {
            layout.txt_date.string = this._resourceData.create_at;
            layout.node_records_layout.active = false;
            layout.node_page.active = false;
            return;
        }
        if(this._data.list.length == 0)
        {
            layout.node_records_layout.active = false;
            layout.node_page.active = false;
            return;
        }
        

        layout.node_records_layout.active = false;
        layout.node_page.active = true;
        

        this._curPage = 1;
        this._totalPage = this._data.totalPages;
        this.onUpdateHistory();

    }
    private onClickClose():void
    {
        this.close();
    }
    private onClickPrePage():void
    {
        if(this._curPage <= 1)
        {
            return;
        }
        this._curPage -= 1;
        this.requestHistoryList();
    }
    private onClickNextPage():void
    {
        
        if(this._curPage >= this._totalPage)
        {
            return;
        }
        this._curPage += 1;
        this.requestHistoryList();
    }
    private onUpdateHistory():void
    {
        let layout = this._layout as Layout_UIHistory;
        layout.btn_pre.node.getComponent(Sprite).grayscale = false;
        layout.btn_next.node.getComponent(Sprite).grayscale = false;
        layout.btn_pre.enabled = true;
        layout.btn_next.enabled = true;
        if(this._curPage == 1)
        {
            layout.btn_pre.node.getComponent(Sprite).grayscale = true;
            layout.btn_pre.enabled = false;
        }
        if(this._curPage == this._totalPage)
        {
            layout.btn_next.node.getComponent(Sprite).grayscale = true;
            layout.btn_next.enabled = false;
        }
        layout.txt_page.string = this._curPage + "/" + this._totalPage;
        layout.node_records_layout.removeAllChildren();

        for(let i = 0; i < this._data.records.length; i++)
        {
            let item = instantiate(layout.recordItem);
            item.setParent(layout.node_records_layout);
            item.setPosition(new Vec3(0));
            item.active = true;
            
            item.getComponent(Layout_UIHistoryDetailsItem).init(this._data.records[i]);
            if(i  % 2 == 0)
            {
                item.getComponent(Layout_UIHistoryDetailsItem).setBgShow(false);
            }
            else
            {
                item.getComponent(Layout_UIHistoryDetailsItem).setBgShow(true);
            }
        }
        
    }


    private requestHistoryList():void
    {
        var req:any = {};
        req.game_code = this._resourceData.game_code;
        req.utc = this._utc;
        req.page = this._curPage;
        req.pageNum = this._pageSize;
        req.startDay = this._resourceData.create_at;
        req.endDay = this._resourceData.create_at;
        
        apiNet.callApi('ReportDayPersonController/selectByDay', {}).then((res) => {
            this.onBindData(res.data);
                
        });
    }
}