import { UITransform } from 'cc';
import { tween } from 'cc';
import { Tween } from 'cc';
import { find } from 'cc';
import { view } from 'cc';
import { Vec3 } from 'cc';
import { UIOpacity } from 'cc';
import { Label } from 'cc';
import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('UITipPanel')
export class UITipPanel extends Component {
    @property(Label)
    label: Label = null;

    private _transform: UITransform = null;
    private _opacity: UIOpacity = null;
    private _timer: number = -1; //定时器
    public isMove = false;
    public isQueue = false;
    public tipStr = '';
    private _scale: Vec3 = new Vec3();

    protected onEnable(): void {
        const canvasTran = find("Canvas")?.getComponent(UITransform);
        const v = view.getVisibleSize();
        const s = v.height / canvasTran.width;
        this.node.scale = new Vec3(s, s, s);
        this._scale = new Vec3(s, s, s);
    }

    init() {
        this._transform = this.node.getComponent(UITransform);
        this._opacity = this.node.getComponent(UIOpacity);
    }

    private move(delay = 1.5) {
        this._opacity.opacity = 0;
        this.isMove = true;
        this.isQueue = false;
        return tween(this.node).by(0, { scale: Vec3.ZERO }).to(.2, { scale: this._scale.clone().multiplyScalar(1.1) }, {
            onUpdate: (_, ratio: number) => {
                this._opacity.opacity = ratio * 255;
            }
        }).to(.2, { scale: this._scale, position: Vec3.ZERO }, {
            onComplete: () => {
                this.isQueue = true;
                this.node.emit('queue');
            }
        }).delay(delay).to(.8, { position: new Vec3(0, this.node.position.y + 500, 0) }, {
            onUpdate: this.onFade.bind(this), onComplete: () => {
                this.isMove = false;
                this.isQueue = false;
                this.node.emit('over');
            }
        });
    }
    public tip(text: string, delay = 1.5) {
        this._opacity.opacity = 0;
        this.node.position = new Vec3(0, -55);
        this.label.string = text;
        this._timer = setTimeout(() => {
            this._transform.width = this.label.getComponent(UITransform).width + 20;
        }, 16);
        tween(this.node).then(this.move(delay)).start();
    }
    public reset(pos: Vec3, delay = 1.5) {
        clearTimeout(this._timer);
        Tween.stopAllByTarget(this.node);
        this._opacity.opacity = 255;
        tween(this.node).to(.5, { position: pos }).then(this.move(delay)).start();
    }

    protected onDestroy(): void {
        clearTimeout(this._timer);
    }
    private onFade(_, ratio: number) {
        this._opacity.opacity = 255 - 255 * ratio;
    }
}


