import { _decorator, Component, Node } from 'cc';
import { Label } from 'cc';
import { Num } from '../../core_tgx/framework/util/Num';
;
const { ccclass } = _decorator;

@ccclass('labNumShow')
export class labNumShow extends Component {
    onLoad() {

    }
    /**小数点后几位展示 100为展示小数点后两位 */
    public pointNum:number=100;
    /**输入值需*100 */
    private _num: number;
    public get num(): number {
        return this._num
    }
    public set num(v: number) {
        this._num = v;
        this.node.getComponent(Label).string = Num.exchangeToThousands(1, v / 100);
    }

    /**输入目标值 */
    setRealNum(v:number){
        this.num = v*this.pointNum;
    }

    /**当前真实值 */
    getRealNum():number{
        return Number(this.node.getComponent(Label).string);
    }
}