import { JsonAsset } from "cc";
import { ResourceMgr } from "../../core_tgx/base/ResourceMgr";
import { game } from "cc";

export class ConfigMgr{
    private static _instance:ConfigMgr;
    private _ruleConfig:any = {};
    private _headConfig:any = {};
    private _roomConfig:any = {};
    private _shopConfig:any = {};
    public static get inst():ConfigMgr{
        if(!this._instance){
            this._instance = new ConfigMgr();
        }
        return this._instance;
    }
    /**游戏规则配置 */
    public getRuleConfig():any
    {
        return this._ruleConfig;
    }
    /**头像配置 */
    public getHeadConfig():any
    {
        return this._headConfig;
    }
    /**房间配置 */
    public getRoomConfig():any
    {
        return this._roomConfig;
    }
    /**商城配置 */
    public getShopConfig():any
    {
        return this._shopConfig;
    }
    /**加载配置 */
    public loadConfig(bundle:string,url:string,callback: Function): void {
       
        ResourceMgr.inst.loadResByModuleBundle(bundle,url,JsonAsset,(err,jsonAsset)=>{
            if(err){
                console.log("load config error",err);
                callback(err);
                return;
            }
            switch(url)
            {
                case "config/ruleconfig":
                    this._ruleConfig = jsonAsset.json;
                    break;
                case "config/headconfig":
                    this._headConfig = jsonAsset.json;
                    break;
            }
            callback();
        });
    }

    /**加载地区配置 */
    public loadAreaConfig(bundle:string,url:string,callback: Function): void {
       
        ResourceMgr.inst.loadAreaConfigByModuleBundle(bundle,url,JsonAsset,(err,jsonAsset)=>{
            if(err){
                console.log("load config error",err);
                callback(err);
                return;
            }
            switch(url)
            {
                case "roomconfig":
                    this._roomConfig = jsonAsset.json;
                    break;
                case "shopconfig":
                    this._shopConfig = jsonAsset.json;
                    break;
            }
            callback();
        });
    }
    

    /**获取头像url */
    public getHeadUrl(headID:number):string
    {
        for(var i:number = 0; i < this._headConfig.length; i++)
        {
            var headData:any = this._headConfig[i];
            if(headData.id == headID)
            {
                return  headData.res;
            }
        }
        return this._headConfig[0].res;
    }
    /**通过gamekey获取房间列表 */
    public getRoomListByGameKey(gamekey:string):any[]
    {
        if(this._roomConfig[gamekey])
        {
            return this._roomConfig[gamekey];
        }
        return null;
    }
}