export interface Player {
    uid: string;// 玩家id
    seat: number;// 玩家座位
    nickname: string;// 昵称
    head: string;// 头像
    gold: number;// 金币变化
    hand_card: number[];// 玩家手牌
    maxCardPlayers: number[];// 手牌最大的赢家(座位号)
    settlement_card: number[];// 结算牌型
    card_type: number | undefined;// 牌型
}

//server -> client
export interface MsgGameOver {
    room_id: string;
    cur_gold: number;
    players: Player[];
    comm_ids: number[];
}