//server -> client
export interface MsgAction {
    round: number;// 当前轮次
    pot: number;// 底池大小 
    seat: number;// 上家玩家座位
    action: number;// 上家操作 1下注 4allin 5过牌 6弃牌
    bet: number;// 上家下注金额
    round_bet: number;// 上家本轮的累计下注
    game_bet: number;// 上家本局游戏的累计下注
    hand_card?: number[];// 全押玩家的手牌
    cur_seat: number;// 当前操作的玩家座位
    can_bet: number;// 是否可下注
    min_bet: number;//最小下注金额（也是最大下注金额）
    can_check: number;// 是否可过牌
    can_call: number;//是否可以跟牌
    call: number;// 跟注金额(当前玩家最小的跟注金额, 如果没有上一个操作玩家，则做为盲注表现，如果跟住金额为0，表示当前无需下注)
    can_raise: number;//是否可以加注
    min_raise: number;// 最小加注金额(当前玩家最小的加注金额为上一家的下注金额-上上家的下注金额) （也是最大加注金额）
    op_time: number;// 操作时间

    desc?: string;//调试ai
}