import { Label } from 'cc';
import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('InfoMgr')
export class InfoMgr extends Component {
    @property(Label)
    giftTime:Label;

    private totalSeconds = 3660; // 总倒计时秒数
    private currentSeconds: number;
    start() {
        this.currentSeconds = this.totalSeconds;
        this.updateLabel(); // 初始化显示
        this.startCountdown();
    }
    
    startCountdown() {
        // 每秒调用一次更新函数
        this.schedule(() => {
            this.currentSeconds--;
            this.updateLabel();
            
            if (this.currentSeconds <= 0) {
                this.unschedule(this.startCountdown); // 倒计时结束后停止调度
                this.giftTime.string = '时间到！';
            }
        }, 1, this.totalSeconds, 0);
    }

    updateLabel() {
        // 格式化时间为 MM:SS 格式
        const minutes = Math.floor((this.currentSeconds % 3600) / 60);
        const seconds = this.currentSeconds % 60;
        const hours = Math.floor(this.totalSeconds / 3600);
        const formattedHours = hours < 10 ? '0' + hours : hours.toString();
        const formattedMinutes = minutes < 10 ? '0' + minutes : minutes.toString();
        const formattedSeconds = seconds < 10 ? '0' + seconds : seconds.toString();
        this.giftTime.string = `${formattedHours}:${formattedMinutes}:${formattedSeconds}`; 
    }

    resetCountdown(seconds: number) {
        this.totalSeconds = seconds;
        this.currentSeconds = seconds;
        this.unschedule(this.startCountdown);
        this.updateLabel();
        this.startCountdown();
    }

    update(deltaTime: number) {
        
    }

    
}


