export class LanguageData {
    /** bundle */
    static bundle: string = "";
    /** JSON资源目录 */
    static path_json: string = "language/json";
    /** 纹理资源目录 */
    static path_texture: string = "language/texture";
    /** SPINE资源目录 */
    static path_spine: string = "language/spine";

    /** 当前语言 */
    static current: string = "";
    /** 语言JSON配置数据 */
    static json: any = {}
    /** 公用语言JSON配置数据 */
    static basicJson: any = {}
    /** 语言EXCEL配置数据 */
    static excel: any = null!;
    /** TTF字体 */
    static font: TTFFont = null!;

    /** 
     * 通过多语言关键字获取语言文本 
     * 
     * 注：
     * 
     * 1、先获取language/json中的配置数据，如果没有到 则去basic下获取
     */
    public static getLangByID(key: string,...paramArr: Array<any>): string {
        let valueStr:string = key;
        if (this.json.hasOwnProperty(key)) {
            valueStr = this.json[key];
        }
        else
        {
            if (this.basicJson.hasOwnProperty(key)) {
                valueStr = this.basicJson[key];
            }
        }
        valueStr = Str.format(valueStr, ...paramArr);
        
        return valueStr;
    }
}

export const LanguageType = [
    'LanguageLabel',
    'LanguageSprite',
]