
//中奖线
export interface PlaySlotPayline {
    //payline id
    id: string;
    //payline 所包含的每个点的坐标
    points: PlaySlotCoordinate[];
    //payline 赢取的钱
    payout: string;
    //激活的符号（双喜临门）
    activePoints?: PlaySlotCoordinate[];
}

//左下角点的坐标为（1， 1）
export interface PlaySlotCoordinate {
    //行号， 从1开始
    row: number;
    //轴号（列号）从1开始
    reel: number;
}

//青一色，全部颜色相同
export interface AllTheSame {
    //青一色id
    id: string;
    //是否为青一色
    isTheSame: boolean;
}

export interface PlaySlotSymbol {
    //符号id
    id: string;
    //符号出现的位置
    coordinate: PlaySlotCoordinate;

    //倍率符号赔付的金额
    payout?: string;

    //金框符号(亡灵大盗)
    isGoldFrame?: boolean;
}


export interface PlaySlotRound {
    symbols: PlaySlotSymbol[];
    paylines: PlaySlotPayline[];
    //本轮新增的符号
    newSymbols?: PlaySlotSymbol[];
}


export interface LineSlotsResult {
    winning: boolean;
    //界面上每个位置停止后最终包含的符号
    symbols: PlaySlotSymbol[];
    payout: string;//赢取的钱

    //所有的中奖线
    paylines: PlaySlotPayline[];

    //剩余的免费次数
    leftFreeCount: number;

    //虚拟幸运事件
    virtualBigEvents?: boolean;
}


