import { _decorator, Component, sys, Node } from 'cc';
import { SubGameMgr } from './SubGameMgr';
import { getSubGameConf, IGameSceneInfo, subGameConf, SubGameDef, SubGameType } from './SubGameDef';
import { Label } from 'cc';
import { Sprite } from 'cc';
import { ResourceMgr } from '../../core_tgx/base/ResourceMgr';
import { ModuleDef } from '../../scripts/ModuleDef';
import { SpriteFrame } from 'cc';
import { director } from 'cc';
import { SubGameMessage } from './SubGameMessage';
import { JSB, NATIVE } from 'cc/env';
import { HotUpdateCtrl } from '../../scripts/hotupdate/HotUpdateCtrl';
import { HotUpdateEvent } from '../../scripts/hotupdate/HotUpdateEvent';
import { GameConst } from '../../scripts/GameConst';
import { LobbyScene } from './LobbyScene';
import { Prefab } from 'cc';
import { instantiate } from 'cc';
import { LanguageData } from './language/LanguageData';
import { LanguageLabel } from './language/LanguageLabel';
const { ccclass, property } = _decorator;

export enum SubGameHotUpdateStatus {
    updateRate,
    updateFinfish,
    updateHighVersion

}
@ccclass('SubGame')
export class SubGame extends Component {

    public gameName: string;
    public gameType: string;
    @property(Prefab)
    gametaghot: Prefab = null;
    @property(Prefab)
    gametagfun: Prefab = null;
    @property(Prefab)
    gametagnew: Prefab = null;
    @property(Node)
    loadingNode: Node;

    private _bundle: string;


    public static subGameHotUpdateCtrls: { [key: string]: HotUpdateCtrl } = {}; //热更控制器map
    public static subGameHotUpdateStatusMap: { [key: string]: SubGameHotUpdateStatus } = {}; //子游戏热更状态map

    public static downSubGame: string = ""; //等待下载的子游戏
    public static canEnterSubGame: boolean = true; //能否下载完后自动进入，1个子游戏下载完成自动进入，多个下载的话不触发自动进入
    async start() {
        this.loadingNode.active = false;
    }

    public initData(data: IGameSceneInfo): void {
        this.gameName = data.id;
        this.gameType = data.gametype;
        this._bundle = data.bundle;
        let txt_title: Node = this.node.children[0].getChildByName("txt_title");
        let tag: Node = this.node.getChildByName("tag")
        if (txt_title) {
            txt_title.getComponent(Label).string = data.name;
            txt_title.getComponent(LanguageLabel).dataID = data.language;
        }
        if (data.tag == "null") {

        }
        if (data.tag == "hot") {
            const prefabInstance = instantiate(this.gametaghot);
            tag.addChild(prefabInstance);
        }
        if (data.tag == "new") {
            const prefabInstance = instantiate(this.gametagnew);
            tag.addChild(prefabInstance);
        }
        if (data.tag == "fun") {
            const prefabInstance = instantiate(this.gametagfun);
            tag.addChild(prefabInstance);
        }
        if (!data.showTitle) {
            txt_title.active = false;
        }

        let newNode: Node = new Node();
        this.node.addChild(newNode);
        
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.BASIC, data.icon + "/spriteFrame", SpriteFrame, (error, resObj: SpriteFrame) => {
            this.node.getComponent(Sprite).spriteFrame = resObj;
        });
        // prefabInstance.getComponent(Sprite).spriteFrame=
        this.node.on(Node.EventType.TOUCH_END, this.onSubGameIconClicked, this);
        director.on(HotUpdateEvent.HotUpdateRate, this.onHotUpdateRate, this);
        director.on(HotUpdateEvent.HotUpdateFinish, this.onHotUpdateFinish, this);
        director.on(HotUpdateEvent.CheckVersionResult, this.onCheckVersionResult, this);
    }
    protected onDestroy(): void {
        director.off(HotUpdateEvent.HotUpdateRate, this.onHotUpdateRate, this);
        director.off(HotUpdateEvent.HotUpdateFinish, this.onHotUpdateFinish, this);
        director.off(HotUpdateEvent.CheckVersionResult, this.onCheckVersionResult, this);
    }
    onSubGameIconClicked(): void {
        if (!NATIVE || !JSB || !GameConst.hotUpdate_Open) {
            director.emit(SubGameMessage.EnterSubGame, this.gameName);
            return
        }
        this.checkSubGameUpdate(this.gameName);
    }
    async checkSubGameUpdate(gameName: string): Promise<void> {

        //不需要每次都去检测更新
        if (!SubGame.subGameHotUpdateStatusMap.hasOwnProperty(gameName)) {
            SubGame.subGameHotUpdateStatusMap[gameName] = SubGameHotUpdateStatus.updateRate;
        }
        else {
            let status = SubGame.subGameHotUpdateStatusMap[gameName]
            if (status == SubGameHotUpdateStatus.updateFinfish) {
                this.enterSubGame();
                return;
            }
            else if (status == SubGameHotUpdateStatus.updateHighVersion) {
                tgx.UIAlert.show(LanguageData.getLangByID("tid_hotupdateVersion"));
                return;
            }
        }

        let ctrl = await this.getSubGameHotUpdateCtrl(this.gameName);
        ctrl.checkUpdate();
    }
    async getSubGameHotUpdateCtrl(subGameName: string) {
        let ctrl = SubGame.subGameHotUpdateCtrls[subGameName];
        if (!ctrl) {
            ctrl = new HotUpdateCtrl();
            SubGame.subGameHotUpdateCtrls[subGameName] = ctrl;
            await ctrl.init(this._bundle);
        }
        return ctrl
    }
    /**
     * 热更新版本检查结果
     */
    async onCheckVersionResult(subGameName: string, newVersionFoud: boolean) {
        if (this._bundle != subGameName) {
            return;
        }
        // this.loadingSubGame = "";
        console.log('-------CheckVersionResult-------', subGameName, newVersionFoud)
        if (newVersionFoud) {
            //如果当前版本号比主版本号高，则直接进入
            if (SubGame.subGameHotUpdateCtrls.hasOwnProperty(this.gameName)) {
                let ctrl: HotUpdateCtrl = SubGame.subGameHotUpdateCtrls[this.gameName];
                console.log(this._bundle + "subgameLocalVersion:" + ctrl.getLocalManifestVersion() + " |subgameReteVersion:" + ctrl.getReteManifestVersion() + "> mainVersion:" + HotUpdateCtrl.mainVersion);
                if (ctrl.getReteManifestVersion() > HotUpdateCtrl.mainVersion) {
                    if (ctrl.getLocalManifestVersion() > 0) {
                        this.enterSubGame();
                        return;
                    }

                    SubGame.subGameHotUpdateStatusMap[this.gameName] = SubGameHotUpdateStatus.updateHighVersion;
                    if (SubGame.subGameHotUpdateCtrls.hasOwnProperty(this.gameName)) {
                        delete SubGame.subGameHotUpdateCtrls[this.gameName];
                    }
                    tgx.UIAlert.show(LanguageData.getLangByID("tid_hotupdateVersion"));
                    return;
                }
            }
            this.loadingNode.active = true;
            let ctrl = await this.getSubGameHotUpdateCtrl(this.gameName)
            ctrl.hotUpdate();
            //1个子游戏下载完成自动进入，多个下载的话不触发自动进入,清除waitEnterSubGame

            if (SubGame.downSubGame == "") {

                SubGame.canEnterSubGame = true;
            }
            else {
                console.log("other subgame is updating:" + SubGame.downSubGame)
                SubGame.canEnterSubGame = false;
            }
            SubGame.downSubGame = this.gameName;

        } else {
            this.enterSubGame();
        }
    }

    /**
     * 热更进度回调
     */
    onHotUpdateRate(subGameName: string, rate: number) {
        if (this._bundle != subGameName) {
            return;
        }
        this.loadingNode.getComponentInChildren<Label>(Label).string = Math.floor(rate * 100) + "%";
    }

    /**
     * 更新完成
     */
    onHotUpdateFinish(subGameName: string, result: boolean) {
        if (this._bundle != subGameName) {
            return;
        }
        this.loadingNode.active = false;
        if (!result) {
            tgx.UIAlert.show(LanguageData.getLangByID("tid_hotupdatefail"));
            return;
        }

        this.enterSubGame();

    }
    private enterSubGame(): void {
        SubGame.subGameHotUpdateStatusMap[this.gameName] = SubGameHotUpdateStatus.updateFinfish;
        if (SubGame.subGameHotUpdateCtrls.hasOwnProperty(this.gameName)) {
            delete SubGame.subGameHotUpdateCtrls[this.gameName];
        }
        //如果有其他的子游戏在更新 不进入
        if (Object.keys(SubGame.subGameHotUpdateCtrls).length > 0) {
            console.log("other subgame is updating")
            return;
        }
        if (!SubGame.canEnterSubGame) {
            if (Object.keys(SubGame.subGameHotUpdateCtrls).length == 0) {
                //都下载完后 重置
                SubGame.canEnterSubGame = true;
                SubGame.downSubGame = "";

            }
            return;
        }
        console.log("enterSubGame:" + this.gameName)
        director.emit(SubGameMessage.EnterSubGame, this.gameName);
    }

}