import { Prefab } from 'cc';
import { Label } from 'cc';
import { Color } from 'cc';
import { Button } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { instantiate } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Layout_UIShop')
export class Layout_UIShop extends Component {
    @property(Button)
    btnClose:Button;//关闭窗口按钮
    @property(Node)
    right:Node;//原先挂载商品的节点（目前不需要）
    @property(Node)
    bginfo:Node;//挂载界面的空节点
    @property(Prefab)
    merchandise:Prefab;//商品预制体 (目前不需要)
    @property(Prefab)
    recommend:Prefab;//推荐
    @property(Prefab)
    chongzhi:Prefab;//充值
    @property(Prefab)
    gift:Prefab;//礼品
    @property(Prefab)
    leichong:Prefab;//累充
    @property({
        type: [Node],
        tooltip: '左侧按钮数组'
    })
    leftButtons: Node[] = [];


    choosebtn(event: TouchEvent, args: any){
        switch(args){//存在冗余代码 后续修改
            case"1":
            for(let i = 0;i<this.bginfo.children.length;i++){
                this.bginfo.children[i].destroy();//先清空挂预制体节点的所有子节点
            }
            for(let i = 0 ; i < this.leftButtons.length ; i++){
                if(i == 0){//设置商店左侧按钮交互；大小；字体颜色
                    this.leftButtons[i].getComponent(Button).interactable = false;
                    this.leftButtons[i].setScale(1.2,1.2);
                    this.leftButtons[i].getChildByName("Label").getComponent(Label).color = new Color(255,255,255);
                }else{
                    this.leftButtons[i].getComponent(Button).interactable = true;
                    this.leftButtons[i].setScale(1,1);
                    this.leftButtons[i].getChildByName("Label").getComponent(Label).color = new Color(118,118,118);
                }
            }
            this.bginfo.addChild(instantiate(this.recommend));
            break;
            case"2"://与上同一逻辑
            for(let i = 0;i<this.bginfo.children.length;i++){
                this.bginfo.children[i].destroy();
            }
            for(let i = 0 ; i < this.leftButtons.length ; i++){
                if(i == 1){
                    this.leftButtons[i].getComponent(Button).interactable = false;
                    this.leftButtons[i].setScale(1.2,1.2);
                    this.leftButtons[i].getChildByName("Label").getComponent(Label).color = new Color(255,255,255);
                }else{
                    this.leftButtons[i].getComponent(Button).interactable = true;
                    this.leftButtons[i].setScale(1,1);
                    this.leftButtons[i].getChildByName("Label").getComponent(Label).color = new Color(118,118,118);
                }
            }
            this.bginfo.addChild(instantiate(this.chongzhi));
            break;
            case"3":
            for(let i = 0;i<this.bginfo.children.length;i++){
                this.bginfo.children[i].destroy();
            }
            for(let i = 0 ; i < this.leftButtons.length ; i++){
                if(i == 2){
                    this.leftButtons[i].getComponent(Button).interactable = false;
                    this.leftButtons[i].setScale(1.2,1.2);
                    this.leftButtons[i].getChildByName("Label").getComponent(Label).color = new Color(255,255,255);
                }else{
                    this.leftButtons[i].getComponent(Button).interactable = true;
                    this.leftButtons[i].setScale(1,1);
                    this.leftButtons[i].getChildByName("Label").getComponent(Label).color = new Color(118,118,118);
                }
            }
            this.bginfo.addChild(instantiate(this.gift));
            break;
            case"4":
            for(let i = 0;i<this.bginfo.children.length;i++){
                this.bginfo.children[i].destroy();
            }
            for(let i = 0 ; i < this.leftButtons.length ; i++){
                if(i == 3){
                    this.leftButtons[i].getComponent(Button).interactable = false;
                    this.leftButtons[i].setScale(1.2,1.2);
                    this.leftButtons[i].getChildByName("Label").getComponent(Label).color = new Color(255,255,255);
                }else{
                    this.leftButtons[i].getComponent(Button).interactable = true;
                    this.leftButtons[i].setScale(1,1);
                    this.leftButtons[i].getChildByName("Label").getComponent(Label).color = new Color(118,118,118);
                }
            }
            this.bginfo.addChild(instantiate(this.leichong));
            break;
        }
    }


    update(deltaTime: number) {
        
    }
}


