import { _decorator, Node, sys, debug, setDisplayStats, profiler, director, dynamicAtlasManager, error, PhysicsSystem2D, EPhysics2DDrawFlags } from 'cc';
const { ccclass, property } = _decorator;
import { SubGameMgr } from 'db://assets/module_basic/scripts/SubGameMgr';
import { SubGameDef } from 'db://assets/module_basic/scripts/SubGameDef';
import { Component } from 'cc';
import Progress from './Progress';

@ccclass('LoadingScene')
export default class LoadingScene extends Component {
    @property({ type: Node })
    private progressNode: Node | null = null;
    onLoad() {
        this.progressNode.active = true;
        tgx.UIMgr.inst.closeAll();
    }
    start() {
        
    }
    public async preLoadRes() {
        let progressScript:Progress =this.progressNode.getComponent(Progress);
        let processFun = progressScript.updateProgress.bind(progressScript);
        processFun(0);
        await SubGameMgr.gameMgr.preLoadRes(processFun);
    }

}
