export class IGameSceneInfo {
    id: string;
    name: string;
    bundle: string;
    icon: string;
    show: boolean;   //是否显示
    showTitle: boolean; //是否显示标题
    tag: string;// hot new  fun
    gametype: string;//fish sport slots
    language: string;
}
export enum SubGameType {
    LEISURE = 'leisure',
    SKILL = 'skill',
    SLOT = 'slot',
    FISH = 'fish',
}


