import { _decorator, Button, Component, Node, Prefab, ToggleContainer } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Layout_UITask')
export class Layout_UITask extends Component {
    @property(ToggleContainer)
    tabs: ToggleContainer;

    @property(Node)
    frame: Node;

    @property(Node)
    canvas: Node;

    @property(Button)
    btnClose: Button;

    @property(Button)
    btnHelp: Button;

    @property([Prefab])
    frameList: Prefab[] = [];

    @property([Prefab])
    canvasList: Prefab[] = [];

    // @property(Prefab)
    // btnClose: Button;
}


