import { ccenum } from "cc";
import { CCFloat } from "cc";
import { Node } from "cc";
import { Component, _decorator } from "cc";

/**
 * @en Layout type.
 *
 * @zh 布局类型。
 */
enum Type {
    /**
     * @en Horizontal layout.
     *
     * @zh 水平布局。
     */
    HORIZONTAL = 1,

    /**
     * @en Vertical layout.
     *
     * @zh 垂直布局。
     */
    VERTICAL = 2,
}
ccenum(Type);

const { ccclass, property } = _decorator;
// 视图层实体是空的
@ccclass('nodePosCenter')
export class nodePosCenter extends Component {
    @property({ type: [Node], tooltip: "子节点数组" })
    public nodeArr: Node[] = [];
    @property({ type: CCFloat, tooltip: "间隔" })
    public dis: number = 0;
    @property({ type: Type, tooltip: "对其类型" })
    public type: Type = Type.HORIZONTAL;

    update() {
        if (this.type == Type.HORIZONTAL) {
            let allWidth: number = 0;
            let showNodeArr: Node[] = [];
            for (let i: number = 0; i < this.nodeArr.length; i++) {
                let temp: Node = this.nodeArr[i];
                if (temp.active) {
                    showNodeArr.push(temp);
                    allWidth += temp.w + this.dis;
                    if (showNodeArr.length > 1) {
                        temp.x = showNodeArr[showNodeArr.length - 2].x + showNodeArr[showNodeArr.length - 2].w / 2 + temp.w / 2 + this.dis;
                    } else {
                        temp.x = 0;
                    }
                }
            }
            for (let i: number = 0; i < showNodeArr.length; i++) {
                showNodeArr[i].x = showNodeArr[i].x - (allWidth / 2 - showNodeArr[0].w / 2);
            }
        } else {
            let allHeight: number = 0;
            let showNodeArr: Node[] = [];
            for (let i: number = 0; i < this.nodeArr.length; i++) {
                let temp: Node = this.nodeArr[i];
                if (temp.active) {
                    showNodeArr.push(temp);
                    allHeight += temp.h + this.dis;
                    if (showNodeArr.length > 1) {
                        temp.y = this.nodeArr[i - 1].y + this.nodeArr[i - 1].h / 2 + temp.h / 2 + this.dis;
                    } else {
                        temp.y = 0;
                    }
                }
            }
            for (let i: number = 0; i < showNodeArr.length; i++) {
                showNodeArr[i].y = showNodeArr[i].y - (allHeight / 2 - showNodeArr[0].h / 2);
            }
        }
    }
}
