import { Vec3 } from 'cc';
import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('select')
export class highlight extends Component {
    // 声明子节点属性
    @property({type: Node})
    public all: Node | null = null;

    @property({type: Node})
    public multiplayer: Node | null = null;

    @property({type: Node})
    public skill: Node | null = null;

    @property({type: Node})
    public slots: Node | null = null;

    @property({type: Node})
    public sports: Node | null = null;

    @property({type: Node})
    public highlight: Node | null = null;

    private targetPos: Vec3 | null = null;

    start() {
        
    }

    update(deltaTime: number) {
        if (this.highlight && this.targetPos) {
            const currentPos = this.highlight.position;
            const newPos = Vec3.lerp(new Vec3(), currentPos, this.targetPos, 0.1);
            this.highlight.setPosition(newPos);
            
            // 当接近目标位置时停止移动
            if (Vec3.distance(newPos, this.targetPos) < 0.1) {
                this.targetPos = null;
            }
        }
    }

    select_click(event: TouchEvent, args: any){
        console.log(this.highlight.getPosition());
        switch(args){
            case "all":
                this.targetPos = new Vec3(-460, 0, 0);
                break;
            case "multiplayer":
                this.targetPos = new Vec3(-220, 0, 0);
                break;
            case "skill":
                this.targetPos = new Vec3(220, 0, 0);
                break;
            case "slots":
                this.targetPos = new Vec3(0, 0, 0);
                break;
            case "sports":
                this.targetPos = new Vec3(460, 0, 0);
                break;
        }
    }
    
    
}