import { _decorator, Component, Label, Sprite, Node } from 'cc';
const { ccclass, property } = _decorator;

import { LanguageData } from 'db://assets/module_basic/scripts/language/LanguageData';
import { SubGameMgr } from '../SubGameMgr';
import { UserMgr } from '../UserMgr';
import { ResourceMgr } from 'db://assets/core_tgx/base/ResourceMgr';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { SpriteFrame } from 'cc';
import { sp } from 'cc';
import { Language } from 'db://assets/module_fish_whale/script/const/enumConst';
import { SubGameDef } from '../SubGameDef';

@ccclass('RoomItem')
export class RoomItem extends Component {
    @property(Label)
    private titleLabel: Label = null!;
    @property(Label)
    private limitLabel: Label = null!;
    @property(Label)
    private betLabel: Label = null!;
    @property(Sprite)
    private img_bg: Sprite = null!;
    @property(sp.Skeleton)
    private spine_bg: sp.Skeleton = null!;
    private _data:any = null;
    start()
    {
        
    }
    public setData(data:any)
    {
        this._data = data;
        this.titleLabel.string = LanguageData.getLangByID(this._data.name);
        this.betLabel.string = LanguageData.getLangByID("room_tip1",this._data.bet);
        let min = Number(data.minGold);
        let max = Number(data.maxGold);
        // if (max == -1) {
        //     this.limitLabel.string = "" + min + "-∞";
        // } else {
        //     this.limitLabel.string = "" + min + "-" + max;
        // }
        this.limitLabel.string = LanguageData.getLangByID("room_tip2",min);
        if(SubGameMgr.gameMgr.gameType == SubGameDef.POKER_DEZHOU)
        {
            this.betLabel.string = LanguageData.getLangByID("room_tip3",this._data.bet/2,this._data.bet);
        }
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.BASIC, this._data.bg + "/spriteFrame", SpriteFrame, (error, resObj: SpriteFrame) => {
            if(!error)
            {
                this.img_bg.spriteFrame = resObj;
            }
        });
        
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.BASIC, this._data.spine, sp.SkeletonData, (error, resObj: sp.SkeletonData) => {
            if(!error)
            {
                this.spine_bg.skeletonData = resObj;
                this.spine_bg.setAnimation(0, 'animation', true);
            }
        });
    }
    private onItemClick():void
    {
        if(!this._data) return;

        if(UserMgr.inst.coin <  this._data.minGold)  {
            //金币不如
            tgx.UIAlert.show(LanguageData.getLangByID("gold_notenough")).onClick(() => {
                
            });
            return;
        }
        if(this._data.maxGold != -1 && UserMgr.inst.coin > this._data.maxGold)
        {
            //金币超出
            tgx.UIAlert.show(LanguageData.getLangByID("gold_exceed")).onClick(() => {
                
            });
            return;
        }
        SubGameMgr.inst.MatchRoom(SubGameMgr.gameMgr.gameType,true,String(this._data.id));
    }
}