import { _decorator, Component, Director, director, Node } from 'cc';
import { gameNet, lobbyNet, NetGameServer, NetLobbyServer } from './NetGameServer';
import { WebsocketClient } from './WebsocketClient';
import { SceneDef } from '../../scripts/SceneDef';
import { RoomMgr } from './RoomMgr';
import { EDITOR, PREVIEW } from 'cc/env';
import { LobbyMgr } from './LobbyMgr';
import { SubGameMgr } from './SubGameMgr';
import { LanguageData } from 'db://assets/module_basic/scripts/language/LanguageData';
import { GameMode } from './GameMgr';
const { ccclass, property } = _decorator;

//主动添加到所有场景。
director.on(Director.EVENT_AFTER_SCENE_LAUNCH, () => {
    if (EDITOR && PREVIEW || !EDITOR) {
        if (director.getScene().getChildByName('NetworkReconnector')) {
            throw new Error('NetworkReconnector can only be added by code, please remove from scene:.' + director.getScene().name);
        }
        let node = new Node('NetworkReconnector');
        node.addComponent(NetworkReconnector);
        director.getScene().addChild(node);
    }
});

@ccclass('NetworkReconnector')
export class NetworkReconnector extends Component {
    start() {
        this.schedule(() => {
            this.checkNetwork();
        }, 1.0);

        gameNet.on(WebsocketClient.EVENT_DISCONNECTED, this.onGameDisconnected, this);
        gameNet.on(WebsocketClient.EVENT_CONNECTED, this.onconnected, this);
        if (lobbyNet.type != 'http') {
            let net = lobbyNet as NetLobbyServer;
            net.on(WebsocketClient.EVENT_DISCONNECTED, this.onLobbyDisconnected, this);
        }
    }

    protected onDestroy(): void {
        gameNet.off(WebsocketClient.EVENT_DISCONNECTED, this.onGameDisconnected, this);
        gameNet.off(WebsocketClient.EVENT_CONNECTED, this.onconnected, this);
        if (lobbyNet.type != 'http') {
            let net = lobbyNet as NetLobbyServer;
            net.off(WebsocketClient.EVENT_DISCONNECTED, this.onLobbyDisconnected, this);
        }
    }
    onconnected() {
        director.emit(WebsocketClient.EVENT_CONNECTED);
    }
    onGameDisconnected(v: { code?: number, reason?: string, isManual?: boolean }) {
        console.log('onGameDisconnected:'+JSON.stringify(v));
        this.onDisconnected(v);
    }
    onLobbyDisconnected(v: { code?: number, reason?: string, isManual?: boolean }) {
        console.log('onLobbyDisconnected:'+JSON.stringify(v));
        this.onDisconnected(v);
    }
    onDisconnected(v: { code?: number, reason?: string, isManual?: boolean }) {
        if (v.reason == 'login_to_other') {
            this.enabled = false;
            RoomMgr.inst.gameMode = GameMode.SOLO;
            
            tgx.UIAlert.show(LanguageData.getLangByID("tid_basic_alert_message28")).onClick(() => {
                tgx.SceneUtil.loadScene(SceneDef.LOGIN);
            });
        }
        else if(v.reason == "room_closed")
        {
            this.enabled = false;
            RoomMgr.inst.gameMode = GameMode.SOLO;
            tgx.UIAlert.show(LanguageData.getLangByID("tid_basic_alert_message29")).onClick(() => {
                RoomMgr.inst.exitRoom();
            });
        }
        else if (v.reason == 'prepare_timeout') {
            this.enabled = false;
            RoomMgr.inst.gameMode = GameMode.SOLO;
            tgx.UIAlert.show(LanguageData.getLangByID("tid_basic_alert_message30")).onClick(() => {
                RoomMgr.inst.exitRoom();
            });
        }
    }

    public get isInGame() {
        return  SubGameMgr.gameMgr && director.getScene().name == SubGameMgr.gameMgr.gameScene?.name;
    }

    private _tryTimes = 0;

    needReconnect(v: { code?: number, reason?: string, isManual?: boolean }) {
        if(!v){
            return true;
        }
        if (v.reason == 'login_to_other') {
            return false;
        }
        if (v.reason == 'normal' || v.reason == 'room_closed') {
            return false;
        }
        if (v.isManual) {
            return false;
        }
        return true;
    }

    private _isReconnecting = false;
    private checkNetwork(): void {

        if (this.isInGame) {
            //额外判断一个是否在连接中，不然会导致一个bug，刚去连接这里就去检测 重连
            if (gameNet.conn && !this._isReconnecting && !gameNet.conn.isConnected && !gameNet.conn["_connecting"]) {
                if (this.needReconnect(gameNet.lastDisconnectReason)) {
                    this._isReconnecting = true;
                    this.enabled = false;
                    this.tryReconnect(gameNet);
                }
            }
        }
        else {
            if(lobbyNet.type != 'http'){
                let net = lobbyNet as NetLobbyServer;
                //额外判断一个是否在连接中，不然会导致一个bug，刚去连接这里就去检测 重连
                if (net.conn && !this._isReconnecting && !net.conn.isConnected && !net.conn["_connecting"]) {
                    if (this.needReconnect(net.lastDisconnectReason)) {
                        this._isReconnecting = true;
                        this.enabled = false;
                        this.tryReconnect(net);
                    }
                }
            }
        }
    }

    private async tryReconnect(net: WebsocketClient) {
        this._tryTimes++;
        if (this._tryTimes > 10) {
            tgx.UIWaiting.hide();
            this.enabled = false;
            // 提示“网络异常，请重新登录”
            tgx.UIAlert.show(LanguageData.getLangByID("tid_basic_alert_message8")).onClick(() => {
                tgx.SceneUtil.loadScene(SceneDef.LOGIN);
            });
            return;
        }
        let isSucc = false;
        if (this.isInGame) {
            if (net == gameNet) {
                console.log('game reconnecting.');
                // 提示“正在重连”
                tgx.UIWaiting.show(LanguageData.getLangByID("tid_basic_wait_message3"));
                //todo
                let ret = await SubGameMgr.inst.enterSubGameRoom(gameNet.authParams);
                if (ret.isSucc) {
                    isSucc = true;
                }
                else if (ret.err.code == 'ROOM_NOT_EXISTS') {
                    RoomMgr.inst.exitRoom();
                    return;
                }
            }
        }
        else {
            console.log('lobby reconnecting.');
            // 提示“正在重连”
            tgx.UIWaiting.show(LanguageData.getLangByID("tid_basic_wait_message3"));
            let ret = await net.ensureConnected();
            isSucc = ret.isSucc;
            /**
             * @en if already login success, need to verify again
             * @zh 如果已经登录成功，则需要再次验证
            */
            if (ret.isSucc) {
                let ret1 = await LobbyMgr.inst.doAuth();
                if (!ret1.isSucc) {
                    tgx.UIWaiting.hide();
                    this.enabled = false;
                    // 提示“网络异常，请重新登录”
                    tgx.UIAlert.show(LanguageData.getLangByID("tid_basic_alert_message8")).onClick(bOK => {
                        tgx.SceneUtil.loadScene(SceneDef.LOGIN);
                    });
                    return;
                }
            }
        }

        /**
         * @en if re-connect failed, try again after 3s
         * @zh 如果没有重连成功，则一定时间后再次尝试
        */
        if (!isSucc) {
            setTimeout(() => {
                this.tryReconnect(net);
            }, 3000);
        }
    }
}

