import { _decorator, Component, Node,Prefab } from 'cc';
import { instantiate } from 'cc';
import { Vec3 } from 'cc';
import { ConfigMgr } from '../ConfigMgr';
import { SubGameMgr } from '../SubGameMgr';
import { RoomItem } from './RoomItem';
import { UITransform } from 'cc';
import { math } from 'cc';
import { Layout } from 'cc';

const { ccclass, property } = _decorator;

@ccclass('RoomContent')
export class RoomContent extends Component {
    @property(Prefab)
    roomItem: Prefab = null;
    @property(Node)
    roomContent: Node = null;
    async start() {
        this.updateRoomList();

    }

    updateRoomList() {
        let roomList:any[] = ConfigMgr.inst.getRoomListByGameKey(SubGameMgr.gameMgr.gameType);
        if(roomList == null) return;
        this.roomContent.removeAllChildren();
        var itemWidth:number = 0;
        for (let index = 0; index < roomList.length; index++) {
            let item = instantiate(this.roomItem);
            item.setParent(this.roomContent);
            item.setPosition(new Vec3(0));
            item.active = true;
            var roomItemScript = item.getComponent(RoomItem);
            roomItemScript.setData(roomList[index]);
            let uiTransform = item.getComponent(UITransform);
            itemWidth = uiTransform.contentSize.width;
        }
        var layout = this.roomContent.getComponent(Layout);
        this.roomContent.getComponent(UITransform).width = layout.paddingLeft+layout.paddingRight + itemWidth * roomList.length + (roomList.length - 1) * layout.spacingX;
        
    }
}


