import { _decorator, Button, Component, EditBox, Label, Node, Sprite, ToggleContainer } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Layout_UIHistoryDetailsItem')
export class Layout_UIHistoryDetailsItem extends Component {

    @property(Label)
    txt_time: Label;
    @property(Label)
    txt_touru:Label;
    @property(Label)
    txt_win:Label;
    @property(Label)
    txt_jiesuan:Label;
    @property(Label)
    txt_game:Label;
    @property(Label)
    txt_order:Label;
    @property(Node)
    node_bg: Node;

    start()
    {
        
    }
    public init(data:any) 
    {
        this.txt_time.string = data.create_at;
        this.txt_touru.string = data.spend;
        this.txt_win.string = data.win;
        this.txt_jiesuan.string = data.final;
        this.txt_game.string = data.game;
        this.txt_order.string = data.sn;
    }
    /**设置背景是否可见 */
    public setBgShow(value:boolean)
    {
        this.node_bg.active = value;
    }
}


