import { SpriteFrame } from 'cc';
import { NodeEventType } from 'cc';
import { Material } from 'cc';
import { Sprite } from 'cc';
import { _decorator, Component, Node } from 'cc';
const { ccclass, property, menu } = _decorator;

export interface IBaseSlotItem {
    layer: number,
    column: number,
    sprite: Sprite
}

/**
 * 基础Slot元素类
 */
@ccclass('BaseSlotItem')
@menu('扩展类/Slot/元素')
export class BaseSlotItem extends Component {
    private _st: Sprite;
    private _bg: Sprite;
    //当前slot中第几个元素
    public column: number;
    //当前第几个layer
    public layer: number;
    public set spriteFrame(v: SpriteFrame) {
        if (!this._st) this._st = this.node.addComponent(Sprite);
        this._st.spriteFrame = v;
    }
    public get spriteFrame() {
        return this._st.spriteFrame;
    }
    public set bgSpriteFrame(v: SpriteFrame) {
        if (!this._bg) {
            this._bg = this.node.addComponent(Sprite);
            this._bg.name = "bg";
            this._bg.node.setSiblingIndex(0);
        }
        this._bg.spriteFrame = v;
    }
    public get bgSpriteFrame() {
        return this._bg.spriteFrame;
    }
    public get sharedMaterial() {
        return this._st.sharedMaterial;
    }
    public set material(v: Material) {
        this._st.material = v;
    }
    public get material() {
        return this._st.material;
    }
    public setSharedMaterial(v, i) {
        this._st.setSharedMaterial(v, i);
    }
    start(): void {
        this.node.on(NodeEventType.TOUCH_END, () => {
            let info: IBaseSlotItem = {
                layer: this.layer,
                column: this.column,
                sprite: this._st
            };
            this.node.emit("click", info);
            // console.log(info);
        });
    }
}


