import { Button } from "cc";
import { GameUILayers } from "../../scripts/GameUILayers";
import { ModuleDef } from "../../scripts/ModuleDef";
import { Layout_UIShop } from "./Layout_UIShop";
import { instantiate } from "cc";
import { Label } from "cc";
import { Color } from "cc";
import { Layout } from "cc";


enum ItemType {
    COIN = "coin",
    DIAMOND = "diamond",
    SKIN = "skin",
    PROP = "prop"
}

interface ShopItemData {
    id: string;                     // 商品ID
    name: string;                   // 商品名称
    type: ItemType;                 // 商品类型
    price: number;                  // 商品价格
    originalPrice?: number;         // 原价（用于显示折扣）
    description: string;            // 商品描述
    icon: string;                   // 商品图标路径
    isOnSale: boolean;              // 是否促销中
    isNew: boolean;                 // 是否新品
    isHot: boolean;                 // 是否热门
    isLocked: boolean;              // 是否锁定
    discount?: number;              // 折扣（0.1-1）
    onPurchase: () => void;         // 购买回调
}

interface UIShopData {
    category: string;               // 当前分类
    items: ShopItemData[];          // 商品列表
    currency: string;               // 货币类型
    balance: number;                // 当前余额
    onCategoryChange: (category: string) => void; // 分类切换回调
}

@tgx_class(ModuleDef.BASIC)
export class UIShop extends tgx.UIController {
    public shopData: UIShopData;
    private static _instance: UIShop = null;
    public defaultTab: 'shop' | 'chongzhi' = 'shop';
    constructor() {
        super('ui_shop/ui_shop', GameUILayers.POPUP, Layout_UIShop);
        UIShop._instance = this; // 初始化时保存实例
    }

    public static getInstance(): UIShop {
        return this._instance;
    }

    protected onCreated(): void {//创建时的回调函数
        let layout = this._layout as Layout_UIShop;
        this.onButtonEvent(layout.btnClose, () => {
            this.close();//绑定关闭按钮
        });
        for (let i = 0; i < layout.leftButtons.length; i++) {//初始化按钮
            layout.leftButtons[i].getComponent(Button).interactable = true;
            layout.leftButtons[i].setScale(1, 1);
            layout.leftButtons[i].getChildByName("Label").getComponent(Label).color = new Color(118, 118, 118);
        }
    
        // 默认进入推荐页或充值页
        if (this.defaultTab === 'chongzhi') {
            this.turnChongzhi();
        } else {
            layout.leftButtons[0].getComponent(Button).interactable = false;
            layout.leftButtons[0].setScale(1.2, 1.2);
            layout.leftButtons[0].getChildByName("Label").getComponent(Label).color = new Color(255, 255, 255);
            layout.bginfo.addChild(instantiate(layout.recommend));
        }
    }

    public turnChongzhi(){
            let layout = this._layout as Layout_UIShop;
            layout.leftButtons[1].getComponent(Button).interactable = false;
            layout.leftButtons[1].setScale(1.2,1.2);
            layout.leftButtons[1].getChildByName("Label").getComponent(Label).color = new Color(255,255,255);
            layout.leftButtons[0].getComponent(Button).interactable = true;
            layout.leftButtons[0].setScale(1,1);
            layout.leftButtons[0].getChildByName("Label").getComponent(Label).color = new Color(118,118,118);
            for(let i = 0;i<layout.bginfo.children.length;i++){
                layout.bginfo.children[i].destroy();
            }
            layout.bginfo.addChild(instantiate(layout.chongzhi));
    }

}