import { _decorator, Button, Component, EditBox, Label, Node, Sprite, ToggleContainer } from 'cc';
import { UIHistoryDetails } from './UIHistoryDetails';
const { ccclass, property } = _decorator;

@ccclass('Layout_UIHistoryItem')
export class Layout_UIHistoryItem extends Component {

    @property(Button)
    btn_go:Button;
    @property(Label)
    txt_date: Label;
    @property(Label)
    txt_cishu:Label;
    @property(Label)
    txt_touru:Label;
    @property(Label)
    txt_win:Label;
    @property(Label)
    txt_jiesuan:Label;
    @property(Node)
    node_bg: Node;

    private  _data:any;

    start()
    {
        this.btn_go.node.on(Node.EventType.TOUCH_END, this.onItemClick, this);
    }
    public init(data:any) 
    {
        this._data = data;
        this.txt_date.string = data.create_at;
        this.txt_cishu.string = data.cishu;
        this.txt_touru.string = data.spend;
        this.txt_win.string = data.win;
        this.txt_jiesuan.string = data.final;
    }
    /**设置背景是否可见 */
    public setBgShow(value:boolean)
    {
        this.node_bg.active = value;
    }
    /**点击明细 */
    public onItemClick()
    {
       tgx.UIMgr.inst.showUI(UIHistoryDetails, null, this, this._data);
    }
}


