import { _decorator, Button, Component, EditBox, Label, Node, Sprite, ToggleContainer} from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Layout_UILanguage')
export class Layout_UILanguage extends Component {
    @property(Button)
    btnMaskBg:Button;  //背景遮罩
    
    @property(Button)
    btnClose: Button;  //关闭按钮

    @property(ToggleContainer)
    toggleLanguage: ToggleContainer;  //语言单选框

    @property(Button)
    btnLanguage:Button;  //选择语言按钮

    @property(Button)
    btnCancel:Button;  //取消
}


