import { Sprite } from 'cc';
import { _decorator, Component, sys, Node } from 'cc';
import { ResourceMgr } from '../../core_tgx/base/ResourceMgr';
import { ModuleDef } from '../../scripts/ModuleDef';
import { SpriteFrame } from 'cc';
import { director } from 'cc';
import { UserMgr } from '../scripts/UserMgr';

const { ccclass, property } = _decorator;

@ccclass('HeadItem')
export class HeadItem extends Component {

    private _data:any;
    @property(Sprite)
    headImg: Sprite;
    private _resObj:SpriteFrame;

    private _isdestory:boolean;
    async start() {
        
    }

    public initData(data: any): void {
        this._isdestory = false;
        this._data = data;
        this.node.getChildByName("img_select").active = false;
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.BASIC, data.res + "/spriteFrame", SpriteFrame, (error, resObj: SpriteFrame) => {
            if(!error)
            {
                if(this._isdestory) return;
                this.headImg.spriteFrame = resObj;
                this._resObj = resObj;
                resObj.addRef();
            }
            
        });
        this.updateSeclet(UserMgr.inst.visualId);

        
        this.node.on(Node.EventType.TOUCH_END, this.onHeadClicked, this);
    }
    public updateSeclet(visualId:number):void
    {
        if(visualId == this._data.id)
        {
            this.node.getChildByName("img_select").active = true;
        }
        else
        {
            this.node.getChildByName("img_select").active = false;
        }
        
    }
    protected onDestroy(): void {

        this._isdestory = true;
        this.headImg.spriteFrame = null;
        if(this._resObj)
        {
            this._resObj.decRef();
            this._resObj = null;
        }
      
    }
    onHeadClicked(): void {
        
        director.emit("onHeadClicked", this._data);
    }
}