export class NetLobbyServer extends WebsocketClient {
    public authParams: ResLogin;
    constructor() {
        super("lobby");
        console.log('Lobby Connection created as Websocket');
    }
}

export class HttpLobbyServer {
    private _globalErrorFilters = {};

    public addErrorFilter(error: string, cb: Function, target?: any) {
        this._globalErrorFilters[error] = { cb: cb, target: target };
    }

    private _http: HttpClient<ServiceType>;
    private _serverUrl = '';

    public authParams: ResLogin;

    public get type() {
        return 'http';
    }

    public get serverUrl() {
        return this._serverUrl;
    }

    constructor() {
        console.log('Lobby Connection created as HTTP');
    }

    createConnection(serverUrls: Readonly<string[]>) {
        let index = Math.floor(serverUrls.length * Math.random());
        let serverUrl = serverUrls[index];
        if(serverUrl == null || serverUrl == '')
        {
            return;
        }
        this._serverUrl = serverUrl;
        this._http = NetUtil.createHttpClient(this._serverUrl);
    }

    public async callApi<T extends string & keyof ServiceType['api']>(apiName: T, req: ServiceType['api'][T]['req'], options?: any): Promise<ApiReturn<ServiceType['api'][T]['res']>> {
        if (this.authParams) {
            req['token'] = this.authParams.token;
        }
        if(GameConst.openNetworkLog)
        {
            console.log(Logger.getDateString()+'[callApi]:', apiName+","+JSON.stringify(req));
        }
        let ret = await this._http?.callApi(apiName, req, options);
        if(GameConst.openNetworkLog)
        {
            console.log(Logger.getDateString()+'[ApiRes]:', apiName+","+JSON.stringify(ret));
        }
        if (!ret.isSucc) {
            let filter = this._globalErrorFilters[ret.err.message];
            if (filter) {
                filter.cb.call(filter.target, ret.err);
            }
        }
        return ret;
    }
}

export class HttpApiServer {
    private _globalErrorFilters = {};

    public addErrorFilter(error: string, cb: Function, target?: any) {
        this._globalErrorFilters[error] = { cb: cb, target: target };
    }

    private _http: HttpClient<ServiceType>;
    private _serverUrl = '';

    public authParams: ResLogin;

    public get type() {
        return 'http';
    }

    public get serverUrl() {
        return this._serverUrl;
    }

    constructor() {
        console.log('Lobby Connection created as HTTP');
    }

    createConnection(serverUrls: Readonly<string[]>) {
        let index = Math.floor(serverUrls.length * Math.random());
        let serverUrl = serverUrls[index];
        if(serverUrl == null || serverUrl == '')
        {
            return;
        }
        this._serverUrl = serverUrl;
    }

    public async callApi(apiName: string, req: any): Promise<any> {
        if(GameConst.openNetworkLog)
        {
            console.log(Logger.getDateString()+'[callApi]:', apiName+","+JSON.stringify(req));
        }
        const xhr = new XMLHttpRequest();
        xhr.open('POST', this._serverUrl, true);
        xhr.setRequestHeader('Content-Type', 'application/json');
        if(this.authParams && this.authParams.apiToken)
        {
            xhr.setRequestHeader('Authorization', `Basic ${this.authParams.apiToken}`); // 添加token到请求头
        }
        var ret = await this.sendPost(apiName,req);
         if(GameConst.openNetworkLog)
        {
            console.log(Logger.getDateString()+'[ApiRes]:', apiName+","+JSON.stringify(ret));
        }
        return ret;
    }
    private async sendPost(apiName: string, req: any): Promise<any>
    {
         return new Promise((resolve, reject) => {
            const xhr = new XMLHttpRequest();
            xhr.open('POST', this._serverUrl+"/"+apiName, true);
            xhr.setRequestHeader('Content-Type', 'application/json');
            
            if(this.authParams && this.authParams.apiToken) {
                xhr.setRequestHeader('Authorization', `Basic ${this.authParams.apiToken}`);
            }
            
            xhr.onreadystatechange = () => {
                if (xhr.readyState === 4) {
                    if (xhr.status >= 200 && xhr.status < 300) {
                        try {
                            const response = JSON.parse(xhr.responseText);
                            resolve(response);
                        } catch (e) {
                            reject({ code: 5001, message: 'JSON解析失败' });
                        }
                    } else {
                        reject({ code: xhr.status, message: `HTTP错误: ${xhr.status}` });
                    }
                }
            };
            
            xhr.onerror = () => {
                reject({ code: 5002, message: '网络错误' });
            };
            
            xhr.ontimeout = () => {
                reject({ code: 5003, message: '请求超时' });
            };
            
            xhr.send(JSON.stringify(req));
        });
    }
}

export class NetGameServer extends WebsocketClient {

    public authParams: GameServerAuthParams;
    constructor() {
        super("game");
    }
    public async connectToRoomServer(params: GameServerAuthParams) {
        this.authParams = params;
        this.createConnection([params.serverUrl]);
        return true;
    }

    public async joinRoomServer(uid: string) { 
        let retJoin = await this.callApi('game/AuthClient', {
            sign: this.authParams.token,
            uid: uid,
            time: this.authParams.time,
            roomId: this.authParams.roomId,
            gameType: this.authParams.gameType,
            subType: this.authParams.subType,

            roleName: UserLocalCache.inst.getRoleName(uid),
            // roleName:"_dangerous_test_",//将 roleName:"_dangerous_test_" 就会触发服务端的幸运状态， 这只是方便客户端测试幸运状态效果用
        });
        return retJoin;
    }

    public async playSlots(gamekey:string,bet: string) {
        let retSpin = await this.callApi('slots/PlaySlots', {
            gameId: gamekey,
            bet: bet,
        });

        return retSpin;
    }

    public async PlayMatch3(gamekey:string,bet: string) {
        let retSpin: ApiReturn<ResPlayMatch3> = await this.callApi('slots/PlayMatch3', {
            gameId: gamekey,
            bet: bet,
        });

        return retSpin;
    }

      
}

export const loginNet = new HttpLobbyServer();
export const lobbyNet = LOBBY_USE_HTTP ? new HttpLobbyServer() : new NetLobbyServer();
export const gameNet = new NetGameServer();
export const apiNet = new HttpApiServer();