import { Label } from 'cc';
import { Color } from 'cc';
import { view } from 'cc';
import { Vec3 } from 'cc';
import { tween } from 'cc';
import { EventTouch } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { Num } from 'db://assets/core_tgx/framework/util/Num';
const { ccclass, property } = _decorator;

@ccclass('SlotAutoPanel')
export class SlotAutoPanel extends Component {
    @property({ type: Node, tooltip: "" })
    public panel: Node = null!;
    @property({ type: Node, tooltip: "" })
    public roundedButton: Node = null!;
    @property({ type: Label, tooltip: "用户金币" })
    public lblUserCoin: Label = null!;
    @property({ type: Label, tooltip: "本局总注" })
    public lblCurBet: Label = null!;
    @property({ type: Label, tooltip: "赢钱金额" })
    public winCoin_lab: Label = null!;

    autoNum: number = 0;
    private _money = 0;
    private _callback: Function = null;

    //汇率
    private _exchangeRate: number = 0;
    public get exchangeRate(): number {
        this._exchangeRate = 1;
        return this._exchangeRate;
    }

    /**累计下注 */
    private _betSum: number = 0;
    public get betSum() {
        return this._betSum;
    }
    public set betSum(v: number) {
        this._betSum = v;
    }

    protected onLoad(): void {
        // const v = view.getVisibleSize();
        // const s = v.height / 1334;
        // this.node.scale = new Vec3(s,s,1);
    }

    protected update(dt: number): void {
        // this.lblUserCoin.string = Num.exchangeToThousands(this.exchangeRate, this._money);
        this.lblCurBet.string = Num.exchangeToThousands(this.exchangeRate, this.betSum);
        // this.winCoin_lab.string = mouse.slotCtrl.winCoin_lab.string;
    }

    private autoChooseColor: number[] = [243, 168, 85];
    //选择自动次数
    onCLick_chooseAutoNum(event: EventTouch, args: any) {
        this.roundedButton.color = new Color(255, 200, 36);
        this.roundedButton.opacity = 255
        this.autoNum = parseInt(args);
        let chooseNode = event.target.parent;
        for (let i in chooseNode.children) {
            chooseNode.children[i].children[0].children[0].color = new Color(100, 100, 110);
        }
        event.target.children[0].children[0].color = new Color(this.autoChooseColor[0], this.autoChooseColor[1], this.autoChooseColor[2]);
    }

    //开始自动
    onCLick_startAuto() {
        if (this.autoNum > 0) {
            this.onClose();
            // mouse.node.startAuto(this.autoNum);
            this._callback && this._callback(this.autoNum);
        }
    }

    private showPos: number;
    private hidePos: number;
    showPanel(money: number, betSum, callback: Function) {
        // mouse.node.audio.playclickopen();
        
        this._money = money;
        this._betSum = betSum;
        this._callback = callback;
        this.lblUserCoin.string = Num.exchangeToThousands(this.exchangeRate, this._money);
        this.winCoin_lab.string = "0";

        this.node.active = true;
        this.showPos = (this.panel.h - this.node.parent.h) / 2;
        // this.hidePos = -this.panel.h / 2;
        this.panel.y = this.showPos - this.panel.h;
        tween(this.panel)
            .to(0.2, { y: this.showPos })
            .start()
        tween(this.node)
            .delay(0.2)
            .call(() => {
                tween(this.panel.children[0])
                    .to(0.2, { opacity: 255 })
                    .start()
            })
            .start()
    }

    hidePanel() {
        tween(this.panel)
            .to(0.2, { y: this.showPos - this.panel.h })
            .call(() => {
                this.node.active = false;
            })
            .start()
    }

    onClose() {
        // mouse.node.audio.playclickclose();
        this.hidePanel();
    }
}


