import { director } from "cc";
import { SceneDef } from "../../scripts/SceneDef";
import { HttpLobbyServer, NetLobbyServer, apiNet, lobbyNet, loginNet } from "./NetGameServer";
import { UserLocalCache } from "./UserLocalCache";
import { UserMgr } from "./UserMgr";
import { NetUtil } from "./NetUtil";
import { SubGameMgr } from "./SubGameMgr";
import { ISceneInfo } from "../../core_tgx/base/SceneUtils";
import { LanguageData } from "./language/LanguageData";
import { LOBBY_USE_HTTP } from "../shared/configs/Constants";
import { RoomMgr } from "./RoomMgr";

export class LobbyMgr {
    private static _inst: LobbyMgr;
    public static get inst(): LobbyMgr {
        if (!this._inst) {
            this._inst = new LobbyMgr();
        }
        return this._inst;
    }

    constructor() {
        if (lobbyNet.type == 'http') {
            (lobbyNet as HttpLobbyServer).addErrorFilter('INVALID_TOKEN', () => {
                // 提示“TOKEN 过期，请重新登录”
                tgx.UIAlert.show(LanguageData.getLangByID("tid_basic_alert_message5")).onClick(() => {
                    tgx.SceneUtil.loadScene(SceneDef.LOGIN);
                });
            });
            (lobbyNet as HttpLobbyServer).addErrorFilter('UNKNOWN_ERROR', () => {
                // 提示“系统错误，请重新登录”
                tgx.UIAlert.show(LanguageData.getLangByID("tid_basic_alert_message6")).onClick(() => {
                    tgx.SceneUtil.loadScene(SceneDef.LOGIN);
                });
            });
        }
    }

    async doLogin(account: string, password: string) {
        let serverList = await NetUtil.getGameServerList();
        loginNet.createConnection(serverList);

        let ret = await loginNet.callApi('login/Login', { account: account, password: password });
        if (ret.isSucc) {
            lobbyNet.authParams = ret.res;
            apiNet.authParams = ret.res;
            apiNet.createConnection(serverList);

            let authRet = await this.doAuth();
            if (!authRet.isSucc) {
                return authRet;
            }
        }
        else {
            if (ret.err.message == 'USER_NOT_EXISTS' || ret.err.message == 'PASSWORD_WRONG') {
                // 提示“用户名或者密码错误！”
                tgx.UIAlert.show(LanguageData.getLangByID("tid_basic_alert_message7"));
            }
            else {
                tgx.UIAlert.show(ret.err.message);
            }
        }
        return ret;
    }

    public async backToLobby(subLobbyScene:ISceneInfo) {
        if(director.getScene().name == subLobbyScene.name) return;
        let ret = await this.doAuth(subLobbyScene);
        if (!ret.isSucc) {
            // 提示“网络异常，请重新登录”
            tgx.UIAlert.show(LanguageData.getLangByID("tid_basic_alert_message8")).onClick(() => {
                tgx.SceneUtil.loadScene(SceneDef.START);
            });
        }
    }

    /**
     * @en If it needs to back to SubGame's Lobby, pass in subLobbyScene.
     * @zh 如果需要返回到子游戏大厅，则传入 subLobbyScene。
    */
    async doAuth(subLobbyScene: ISceneInfo = SceneDef.LOBBY) {
        if (!lobbyNet.authParams) {
            return { isSucc: false };
        }

        lobbyNet.createConnection([lobbyNet.authParams.lobbyUrl]);
        apiNet.createConnection([lobbyNet.authParams.apiUrl]);

        if (lobbyNet.type != 'http') {
            let net = lobbyNet as NetLobbyServer;
            let ret2 = await net.ensureConnected();
            if (!ret2.isSucc) {
                tgx.UIAlert.show(ret2.err.message);
                return ret2;
            }
        }
        
        let ret = await lobbyNet.callApi('lobby/Auth', lobbyNet.authParams);
        if (!ret.isSucc) {
            tgx.UIAlert.show(ret.err.message);
            return ret;
        }

        UserMgr.inst.setUserInfo(ret.res.userInfo);

        //没有名字，表示还未创建角色，则进入角色创建流程
        if (!UserMgr.inst.name) {
            // 提示“角色准备中”
            tgx.UIWaiting.show(LanguageData.getLangByID("tid_basic_wait_message1"));
            await tgx.SceneUtil.loadScene(SceneDef.CREATE_ROLE);
        }
        //如果角色在房间中，则进入房间
        else if (ret.res.roomId) {
            let ret2: any = await this.doTryEnterRoom(ret.res.roomId);
            if (ret2.isSucc == false) {
                //进入大厅
                if(subLobbyScene)
                {
                    if(!LOBBY_USE_HTTP && !(lobbyNet as NetLobbyServer).isConnected)
                    {
                        RoomMgr.inst.exitRoom();
                    }
                    else
                    {
                        await tgx.SceneUtil.loadScene(subLobbyScene);
                    }
                    
                }

            }
        }
        else {
            //进入大厅
            if(subLobbyScene)
            {
                await tgx.SceneUtil.loadScene(subLobbyScene);
            }
        }
        return { isSucc: true };
    }

    async rpc_CreateRole(name: string, visualId: number) {
        let ret = await lobbyNet.callApi('lobby/CreateRole', { name: name, visualId: visualId });
        if (ret.isSucc) {
            UserMgr.inst.setUserInfo({ name: ret.res.name, visualId: ret.res.visualId } as any);
        }
        return ret;
    }

    async doTryEnterRoom(id: string, password?: string): Promise<any> {
        // 提示“进入世界”
        tgx.UIWaiting.show(LanguageData.getLangByID("tid_basic_wait_message2"));
        let ret = await lobbyNet.callApi('lobby/TryEnterRoom', { id: id, password: password }, { timeout: 10000 });
        tgx.UIWaiting.hide();

        if (ret.isSucc) {
            let params = ret.res;
            // 提示“进入世界”
            tgx.UIWaiting.show(LanguageData.getLangByID("tid_basic_wait_message2"));
            return await SubGameMgr.inst.loginJumpEnterSubGameRoom(params);
        }
        else {
            tgx.UIAlert.show(ret.err.message);
        }

        return ret;
    }

    async rpc_QuickPlay(type: string, immediate?: boolean) {
        let ret = await lobbyNet.callApi("lobby/StartMatch", { type: type, immediate: immediate });
        return ret;
    }

    async rpc_QuickPlayCancel() {
        let ret = await lobbyNet.callApi("lobby/CancelMatch", {});
        return ret;
    }

    async rpc_CreateRoom(type: string, name: string, password: string,subType:string = null) {
        let ret = await lobbyNet.callApi("lobby/CreateRoom", { roomName: name, gameType: type,password: password,subType:subType});
        return ret;
    }

    /**匹配 */
    async rpc_MatchRoom(type: string,subType:string = null) {
        let ret = await lobbyNet.callApi("lobby/StartMatch", { type: type, subType:subType});
        return ret;
    }

    async rpc_GetAnnouncement(type: string) {
        let ret = await lobbyNet.callApi("lobby/GetAnnouncement", { type: type });
        return ret;
    }

    async rpc_GetNotice() {
        let ret = await lobbyNet.callApi("lobby/GetNotice", {});
        return ret;
    }

    async rpc_GetRoomList(gameType: string, curPage: number, pageItemNum: number) {
        let ret = await lobbyNet.callApi("lobby/GetRoomList", { type: gameType, curPage: curPage, pageItemNum: pageItemNum });
        return ret;
    }
}