import { SubGameMgr } from "./SubGameMgr";

export class GameMode {
    public static SOLO: string = 'solo';
    public static AI: string = 'ai';
    public static ONLINE: string = 'online';
}

export class GameMgr {

    protected _gameType:string;
    protected _entryScene: tgx.ISceneInfo;// 游戏入口场景 不设置的话 直接进入游戏场景
    protected _gameScene: tgx.ISceneInfo;
    protected _loadingScene: tgx.ISceneInfo;
    protected _subType:string;  // 子游戏类型 初级房高级房等

    /**
     * @en type of subgame
     * @zh 子游戏类型
    */
    public get gameType():string{
        return this._gameType;
    }
    /**
     * @en 子游戏类型 初级房高级房等
     * @zh subType
    */
    public get subType():string{
        return this._subType;
    }
    /**
     * @en 子游戏类型 初级房高级房等
     * @zh subType
    */
    public set subType(id:string){
        this._subType = id;
    }
    /**
     * 子游戏的加载场景
     */
    public get loadingScene(): tgx.ISceneInfo {
        return this._loadingScene;
    }
    /**
     * @en entry scene of subgame,e.g. lobby
     * @zh 子游戏的入口场景，比如 大厅
    */
    public get entryScene(): tgx.ISceneInfo {
        return this._entryScene;
    }

    /**
     * @en gameplay scene of subgame
     * @zh 子游戏的游戏玩法场景
    */
    public get gameScene(): tgx.ISceneInfo {
        return this._gameScene;
    }
    
    /**
     * @en reset game data
     * @zh 重置游戏数据
    */
    public reset() {

    }

    public init()
    {

    }
    public uinit()
    {
        
    }
    // 进入游戏
    public async enterGame() {
        SubGameMgr.inst.CreateRoom(this.gameType, true);
    }
    /**
     * 加载资源
     * porcessFun 有2个参数 process(0-1), msg: string ="游戏加载中，请稍后..."
     */
    public async preLoadRes(process:Function):Promise<boolean> {
        return true;
    }
}