import { _decorator, Button, Component, EditBox, Label, Node, Sprite, ToggleContainer } from 'cc';
import { LanguageData } from '../scripts/language/LanguageData';
const { ccclass, property } = _decorator;

@ccclass('Layout_UIHistoryOptionItem')
export class Layout_UIHistoryOptionItem extends Component {

    @property(Label)
    txt_title: Label;

    private _data:any;
    private _cb:Function;
    private _type:number;
    start()
    {
        this.node.on(Node.EventType.TOUCH_END, this.onItemClick, this);
    }
    public init(data:any,type:number,cb:Function) 
    {
        this._data = data;
        if(type == 3)
        {
            this.txt_title.string = data.name;
        }
        else if(type == 1)
        {
            this.txt_title.string = LanguageData.getLangByID(data.type);
        }
        else if(type == 2)
        {
            this.txt_title.string = LanguageData.getLangByID(data.language);
        }
        
        this._cb = cb;
        this._type = type;
    }
    public onItemClick()
    {
       if(this._cb)
       {
          this._cb(this._data,this._type);
       }
    }
}


