import { _decorator, Component, Node } from 'cc';
import { SceneDef } from '../../scripts/SceneDef';
import { UISettings } from '../ui_settings/UISettings';
import { UINotice } from '../ui_notice/UINotice';
import { UIMail } from '../ui_mail/UIMail';
import { UIEnterRoom } from '../ui_enter_room/UIEnterRoom';
import { UITask } from '../ui_task/scripts/UITask';
import { UIdailycheck } from '../ui_dailycheck/UIdailycheck';
import { UIShop } from '../ui_shop/UI_Shop';
import { SubGameMgr } from './SubGameMgr';
import { ProgressBar } from 'cc';
import { tween } from 'cc';

const { ccclass, property } = _decorator;

@ccclass('MenuBar')
export class MenuBar extends Component {
    @property(Node) 
    MoreBtn: Node;
    private moreFlag:boolean = false;
    start() {

    }

    update(deltaTime: number) {
        
    }

    onBtnSettingsClicked(){
        tgx.UIMgr.inst.showUI(UISettings);
    }

    onBtnNoticeClicked(){
        tgx.UIMgr.inst.showUI(UINotice);
    }

    onBtnMailClicked(){
        tgx.UIMgr.inst.showUI(UIMail);
    }

    onBtnTaskClicked(){
        tgx.UIMgr.inst.showUI(UITask);
    }

    onBtnLuckyClicked(){
        tgx.UIMgr.inst.showUI(UITask);
    }

    onBtnShopClicked(){
        tgx.UIMgr.inst.showUI(UIShop);
        
    }
    onBtnCoinClicked(){
        tgx.UIMgr.inst.showUI(UIShop);
    }

    onBtnDailyClicked(){
        tgx.UIMgr.inst.showUI(UIdailycheck);
    }

    onBtnEnterRoomClicked(){
        tgx.UIMgr.inst.showUI(UIEnterRoom);
    }

    onBtnBackClicked(){
        tgx.SceneUtil.loadScene(SceneDef.LOBBY);
    }

    onBtnMoreClicked(){
        if(this.moreFlag == false){
            this.MoreBtn.active = true;
            tween(this.MoreBtn.getComponent(ProgressBar))
                           .by(0.2, { progress: 1}) 
                           .start();
            this.moreFlag = true;
        }else{
            tween(this.MoreBtn.getComponent(ProgressBar))
            .by(0.2, { progress: 0.5})
            .call(() => {  
                this.MoreBtn.getComponent(ProgressBar).progress = 0;
                this.MoreBtn.active = false;
                this.moreFlag = false;
            }) 
            .start();

        }
    }
}


