import { ScrollView } from 'cc';
import { Prefab } from 'cc';
import { _decorator, Button, Component, EditBox, Label, Node, Sprite, ToggleContainer } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Layout_UIHistory')
export class Layout_UIHistory extends Component {

    @property(Button)
    btnClose: Button;
    @property(Node)
    norecords: Node;
    @property(Node)
    records: Node;
    @property(Node)
    node_records_layout: Node;
    @property(Node)
    node_page: Node;
    @property(Prefab)
    recordItem: Prefab;
    @property(Button)
    btn_pre:Button;
    @property(Button)
    btn_next:Button;
    @property(Label)
    txt_page: Label;
    @property(Label)
    txt_cishu:Label;
    @property(Label)
    txt_touru:Label;
    @property(Label)
    txt_win:Label;
    @property(Label)
    txt_jiesuan:Label;
    @property(Button)
    btnSelectClass: Button;
    @property(Button)
    btnSelectGame: Button;
    @property(Button)
    btnSelectDate: Button;
    @property(Label)
    txt_selectClass: Label;
    @property(Label)
    txt_selectGame: Label;
    @property(Label)
    txt_selectDate: Label;
    @property(Node)
    node_dropdownselect: Node;
    @property(Node)
    node_options_layout: Node;
    @property(ScrollView)
    node_options_layout_scrollview: ScrollView;
    @property(Node)
    node_mask_dropdownselect: Node;  
    @property(Prefab)
    optionitem: Prefab;
    @property(Node)
    node_dropdownselectPosClass: Node;
    @property(Node)
    node_dropdownselectPosGame: Node;
    @property(Node)
    node_dropdownselectPosDate: Node;
}


